.modal {
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
  border-radius: 4px !important;
  max-width: 90% !important;
  width: 600px;
  overflow: hidden;

  &__container {
    padding: 40px 40px 30px;
    @include sp {
      padding: 20px 5%;
    }

    &--npdbottom {
      padding-bottom: 5px;
    }

    &--40 {
      padding: 40px;
      @include sp {
        padding: 20px 5%;
      }
    }
  }

  &--big {
    width: 1080px;

    .modal__container {
      padding: 30px 40px 40px;
      @include sp {
        padding: 20px 5%;
      }
    }
  }

  &--w842 {
    width: 842px;

    .modal__container {
      padding: 30px 40px 40px;
      @include sp {
        padding: 20px 5%;
      }
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 13px;
    border-bottom: 1px solid #d5d5d5;
    font-family: $hiragino;

    &--start {
      align-items: flex-start;
      justify-content: flex-start;
      flex-wrap: wrap;
      padding-bottom: 20px;

      h1 {
        margin-right: 40px;
      }

      .checkbox {
        margin-top: 5px;
      }
    }

    &--no-border {
      border-bottom: 0;
    }

    &--wrap-sp {
      padding-bottom: 22px;
      @include sp {
        flex-direction: column;
      }

      .button {
        margin-right: 0;
        @include sp {
          margin: 5px 0 0;
        }
      }
    }
  }

  &__title-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    font-size: 20px;

    &--arrow-left {
      position: relative;
      padding-left: 28px;
  
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 20px;
        height: 16px;
        background: url('../img/icons/icon-arrow-left-black.svg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        transform: translateY(-50%);
      }
    }

    &--big {
      font-size: 24px;
    }

    &--center {
      text-align: center;
    }
  }

  &__subtitle {
    font-family: $hiragino;
    background-color: transparent;
    border: 0;
    font-size: 12px;
    cursor: pointer;
    outline: none;

    &--big {
      font-size: 16px;
      margin-top: 10px;
      display: block;
    }
  }

  &__content {
    margin-top: 17px;

    &.mt0 {
      margin-top: 0;
    }

    &.mb40 {
      margin-bottom: 40px;
    }
  }

  &__label {
    font-size: 16px;
    margin-bottom: 8px;
    font-weight: $semibold;
    display: block;

    &--marginb {
      margin-bottom: 18px;
    }

    &--f14 {
      font-size: 14px;
    }

    &.light-gray {
      color: #555;
    }
  }

  &__textarea {
    width: 100%;
    height: 396px;
    border: 1px solid #d5d5d5;
    border-radius: 4px;
    padding: 8px 15px;
    font-size: 14px;
    font-family: $hiragino;
    resize: none;
    outline: none;
    @include sp {
      height: 250px;
    }

    &::placeholder {
      opacity: 0.3;
      color: #333;
    }
  }

  &__button-container {
    display: flex;
  }

  &__button {
    flex: 1;
    height: 50px;
    background-color: $blue;
    color: $white;
    border: 0;
    cursor: pointer;
    font-family: $hiragino;

    &--cancel {
      background-color: #d5d5d5;
      color: $gray;
    }

    &--tundora {
      background-color: $tundora;
    }
  }

  &__residence-label {
    display: flex;
    align-items: center;
    height: 58px;
    padding: 0 11px;
    cursor: pointer;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      width: 10.92px;
      height: 100%;
      right: 10px;
      background: {
        image: url('../img/icons/ic-blue-down.svg');
        size: 100% auto;
        repeat: no-repeat;
        position: center;
      }
    }
  }

  &__residence-content {
    max-height: 0;
    overflow: hidden;
    transition: 0.3s ease;
  }

  &__residence-item {
    border-bottom: 1px solid #d5d5d5;

    &:first-child {
      border-bottom: 1px solid #d5d5d5;
    }

    &.active {
      .modal__residence-content {
        max-height: unset;
      }

      .modal__residence-label {
        &::after {
          transform: rotate(180deg);
        }
      }
    }
  }

  &__residence-radio {
    padding: 0 30px 9px;
    display: flex;
    flex-wrap: wrap;

    label {
      margin: 0 20px 11px 0;
    }
  }

  &__checkbox {
    span {
      font-weight: $bold;
      color: $blue !important;
    }
  }

  &__table {
    border-collapse: collapse;
    width: 100%;

    &--borderless {
      tr,
      td {
        border: 0;
      }
    }
  }

  &__tr {
    &--space {
      height: 20px;
    }

    &--bordered {
      border: 1px solid #d5d5d5;

      & > .modal__td {
        border: 0;
      }
    }
  }

  &__td {
    border: 1px solid #d5d5d5;
    font-family: $hiragino;
    font-size: 12px;
    letter-spacing: 0.04em;
    padding: 5px 15px;
    min-width: 350px;
    vertical-align: initial;
    @include sp {
      min-width: unset;
    }

    &.no-borderR {
      border-right: none;
    }

    &.blue-bg {
      background-color: #F5FAFF;
    }

    &.gray-bg {
      background-color: #FAFAFA;
    }

    &.blue {
      color: #005BAB;
    }

    &.gray {
      color: #999999;
    }

    &--center {
      vertical-align: middle;
    }

    &--top {
      vertical-align: top;
    }

    &--label {
      background-color: #555;
      color: $white;
      font-size: 12px;
      font-weight: $semibold;
      padding: 5px 13px;
      width: 150px;
      min-width: unset;
      @include sp {
        width: 35%;
      }
    }

    &--p14 {
      padding: 14.5px 15px;
    }

    &--p10 {
      padding: 10px 15px;
    }

    &--big {
      width: 340px;
      padding-right: 25px;
      @include sp {
        width: 35%;
        padding-right: 5px;
      }
    }

    &--185 {
      width: 185px;
    }

    &--200 {
      width: 200px;
    }

    &--20P {
      width: 20%;
    }

    &--220 {
      width: 220px;
    }

    &--25P {
      width: 25%;
    }

    &--50P-SP {
      @include sp {
        min-width: 50%;
      }
    }

    &--35P-SP {
      @include sp {
        width: 35%;
      }
    }

    &--nominwidth {
      min-width: 0;
    }

    &--font14 {
      font-size: 14px;
      padding: 8px 15px;
    }

    .small {
      font-size: 10px;
    }

    .m35 {
      margin-left: 35px;
      @include sp {
        margin-left: 0;
        display: block;
      }
    }
  }

  &__table-text {
    max-width: 660px;

    &--full {
      max-width: 100%;
    }

    &--textarea {
      max-width: 100%;
    }
  }

  &__table-block {
    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &--m40 {
      margin-top: 40px;
    }

    &--mb40 {
      margin-bottom: 40px !important;
    }

    &--mt15 {
      margin-top: 15px;
    }

    &--mb30 {
      margin-bottom: 30px !important;
    }
  }

  &__table-button-wrapper {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    @include sp {
      flex-direction: column;
    }

    .button:not(:last-child) {
      @include sp {
        margin-bottom: 10px;
      }
    }

    &--right {
      justify-content: flex-end;
    }

    &--m20 {
      margin-top: 20px;
    }
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 20px;
    cursor: pointer;
  }

  &__text {
    font-size: 14px;
    margin-top: 7px;
    display: block;
  }

  &__pattern {
    width: 100%;
    height: 392.99px;
    overflow: auto;
    padding: 0 29.5px;
    margin: 17px 0 30px;
    background-color: #FAFAFA;
    border: 1px solid #D5D5D5;
    font-family: $noto-sans;
    @include sp {
      padding: 0 20px;
    }

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: #E0E0E0;
      border-radius: 100px;
    }

    &::-webkit-scrollbar-thumb {
      background: #004B9E;
      border-radius: 100px;
    }
  }

  &__pattern-block {
    padding: 20px 0;
    @include sp {
      padding: 15px 0;
    }

    &:not(:last-child) {
      border-bottom: 1px solid #D5D5D5;
    }
  }

  &__pattern-title {
    font-size: 16px;
    margin-bottom: 11px;
  }

  &__pattern-item {
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__pattern-number {
    font-size: 14px;
    width: 30px;
    flex-shrink: 0;
  }

  &__user {
    margin-bottom: 20px;
  }

  &__user-header {
    padding-bottom: 20px;
    display: flex;
    @include sp {
      flex-direction: column;
    }

    &--col {
      flex-direction: column;
    }

    &--border {
      padding: 10px 0 0;
      border-top: 1px solid #D5D5D5;
      border-bottom: 0;
      margin-top: 20px;
    }
  }

  &__user-note {
    display: block;
    text-align: right;
    font-weight: $bold;
    font-size: 14px;
    margin: 8px 35px 6px 0;
    @include sp {
      margin-right: 0;
      margin-bottom: 20px;
    }

    .red {
      color: #FF334B;
    }
  }

  &__user-label {
    width: 240px;
    font-size: 14px;
    height: 40px;
    display: flex;
    align-items: center;
    font-weight: $bold;
    flex-shrink: 0;
    @include sp {
      width: 100%;
      height: auto;
      margin-bottom: 10px;
    }

    &--hidden {
      opacity: 0;
    }
  }

  &__email-content {
    width: 100%;
  }

  &__user-content {
    width: 100%;
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .js-duplicate {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    @include sp {
      flex-wrap: wrap;
    }

    .checkbox {
      flex-shrink: 0;
      margin-left: 20px;
      @include sp {
        margin-left: 0;
      }
    }

    button {
      flex-shrink: 0;
      margin-left: 20px;
      @include sp {
        position: absolute;
        margin-left: 0;
        top: 0;
        right: 0;
      }

      &.hide {
        display: block;
        opacity: 0;
        pointer-events: none;
      }
    }

    &:not(:last-child) {
      margin-bottom: 10px;
      @include sp {
        margin-bottom: 20px;
      }
    }
  }

  &__user-button {
    margin: 10px 0 20px 240px;
    @include sp {
      margin-left: 0;
    }
  }

  &__user-container {
    margin-top: 17px;

    &--border {
      border-top: 1px solid #D5D5D5;
      padding-top: 18px;
    }
  }

  &__user-title {
    font-size: 14px;
  }

  &__user-main-button {
    border-bottom: 1px solid #D5D5D5;
    padding-bottom: 20px;

    &--last {
      border-bottom: 0;
      padding-bottom: 0;
      padding-top: 20px;
    }
  }

  &__user-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }

  &__user-total {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: $bold;
  }

  &__total-icon {
    margin-right: 10px;
    cursor: pointer;
  }

  &__block {
    &:not(:last-child) {
      margin-bottom: 17px;
    }
  }

  &__select {
    display: flex;
    align-items: center;
    margin-top: 7px;
    @include sp {
      flex-direction: column;
    }

    &.hide {
      display: none;
    }
  }

  &__note {
    font-size: 11px;
    margin-left: 10px;
  }
}
