.tab {
  @include max-width;
  margin-bottom: 60px;
  @include sp {
    margin-bottom: 30px;
  }

  &--top {
    margin-top: 37px;
  }

  &__title {
    min-width: 10%;
    height: 50px;
    font-size: 14px;
    color: $white;
    background-color: #9b9b9b;
    @include center;
    @include sp {
      min-width: 33.2%;
      border-right: 1px solid $white;
      border-bottom: 1px solid $white;
    }

    &:not(:last-child) {
      border-right: 1px solid $white;
    }

    &.active {
      background-color: $white;
      color: $blue;
      border-top: 4px solid $blue;
      font-weight: $bold;
    }
  }

  &__header {
    display: flex;
    flex-wrap: wrap;

    &--big {
      .tab__title {
        min-width: 240px;
        @include sp {
          min-width: calc(100% / 3);
          text-align: center;
        }
      }
    }
  }

  &__content {
    background-color: $white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  }

  &__container {
    max-height: 0;
    overflow: hidden;
    position: relative;

    &.active {
      max-height: unset;
      padding: 60px;
      @include sp {
        padding: 30px;
      }
    }

    &--small-padding {
      &.active {
        padding: 40px;
        @include sp {
          padding: 10px;
        }
      }
    }
  }

  &__chart-main {
    display: flex;
    @include sp {
      flex-direction: column;
      margin: 0 auto 20px;
      align-items: center;
    }
  }

  &__chart-list {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 43px);
    @include sp {
      justify-content: center;
      width: 100%;
    }
  }

  &__chart-item {
    display: flex;
    align-items: center;
    margin: 0 43px 40px 0;
    @include sp {
      flex-direction: column;
      margin: 0 auto 20px;
    }
  }

  &__note {
    position: absolute;
    right: 40px;
    bottom: 38px;
    font-size: 10px;
    @include sp {
      position: static;
      display: block;
      margin-top: 10px;
    }
  }
}
