.title {
  position: relative;
  @include max-width;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &--start {
    align-items: flex-start;
    @include sp {
      flex-direction: column;
    }
  }

  &--end {
    justify-content: flex-end;
  }

  .button {
    margin-right: 0;
    flex-shrink: 0;
  }

  &--mt11 {
    margin-top: 11px;
  }

  &--wide {
    width: 1240px;
    max-width: 100%;
    @include sp {
      max-width: 90%;
    }
  }

  &--full {
    width: 100%;
    max-width: 100%;
  }

  &--relative {
    position: relative;
  }

  &--column-sp {
    @include sp {
      flex-direction: column;
      align-items: flex-start;
    }

    .button__wrapper {
      @include sp {
        margin-top: 10px !important;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
      }

      .button {
        @include sp {
          width: 100%;
          margin: 0 0 5px;
        }
      }
    }
  }

  h3 {
    font: {
      size: 20px;
      weight: $bold;
    }
  }

  &--arrow-left {
    position: relative;
    padding-left: 28px;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 20px;
      height: 16px;
      background: url('../img/icons/icon-arrow-left-black.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      transform: translateY(-50%);
    }
  }

  &__right {
    display: flex;
    align-items: center;
  }

  &__links {
    display: flex;
    align-items: center;
    margin-right: 10.7px;
    @include sp {
      flex-direction: column;
      margin-right: 0;
      margin-left: 5px;
    }
  }

  &__link {
    padding: 0 10px;
    font-size: 12px;
    color: #9B9B9B;
    line-height: 1em;
    @include sp {
      padding: 5px 0;
      line-height: 1.5em;
      text-align: center;
      width: 100%;
    }
  
    &:not(:last-child) {
      border-right: 1px solid #222C34;
      @include sp {
        border-right: 0;
        border-bottom: 1px solid #222C34;
      }
    }

    &--active {
      color: #005BAB;
    }
  }
}
