.chart {
  width: 300px;
  height: 300px;
  max-width: 100%;

  &--full {
    width: 100%;
    height: 380px;
    margin-bottom: 20px;
  }

  &--margin-bottom {
    margin-bottom: 40px;
  }

  &--small {
    width: 160pX;
    height: 160px;
  }

  &--scroll {
    width: auto;
    height: 100%;
  }

  &--h440 {
    height: 498px;
    @include sp {
      height: 300px;
    }
  }

  &--no-margin {
    margin-bottom: 0;
  }

  &__container {
    margin-right: 40px;
    @include sp {
      margin-right: 0;
      margin-bottom: 20px;
    }

    &--small {
      margin-right: 20px;
      @include sp {
        margin-right: 0;
        margin-bottom: 10px;
      }
    }
  }

  &__info {
    margin-top: 12px;

    &--small {
      margin-top: 0;
    }
  }

  &__title {
    font-size: 14px;
    font-weight: $semibold;
    margin-bottom: 8px;
  }

  &__label-item {
    display: flex;
    align-items: center;
    font-size: 12px;
    line-height: getem(12, 20);
  }

  &__color {
    width: 15px;
    height: 10px;
    margin-right: 5px;
  }

  &__row {
    border-bottom: 1px solid #eee;
    padding-bottom: 20px;
    margin-bottom: 40px;
    position: relative;

    .button__wrapper {
      @include sp {
        flex-direction: column;
        align-items: flex-start !important;
      }

      .button__group {
        @include sp {
          margin-top: 20px;
        }
      }

      .button__group > .button__text span {
        color: #333;
      }
    }
  }

  &__text-label {
    font-size: 10px;
    display: block;
    text-align: right;
    margin-bottom: 30px;
  }

  &__wrapper {
    display: none;
    padding-top: 10px;
    position: absolute;
    top: 40px;
    right: 0;
    z-index: 1;
    @include sp {
      top: 80px;
      padding-top: 20px;
    }

    &.show {
      display: block;
    }
  }

  &__wrapper-content {
    width: 400px;
    padding: 40px 40px 30px;
    background-color: #F4F4F4;
    border: 1px solid #D5D5D5;
    border-radius: 4px;
    position: relative;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    @include sp {
      width: 100%;
      padding: 20px 30px;
    }

    &::before {
      content: '';
      width: 50px;
      height: 22px;
      background: {
        image: url('../img/icons/wrapper-arrow.svg');
        size: cover;
        repeat: no-repeat;
      };
      position: absolute;
      top: -22px;
      right: 50px;
    }
  }

  &__wrapper-title {
    font: {
      family: $hiragino;
      size: 20px;
      weight: $medium;
    }
    @include sp {
      font-size: 18px;
    }
  }

  &__list {
    margin-top: 15px;
    @include sp {
      margin-top: 10px;
    }

    &--inner {
      .chart__item {
        border-top: none;
        padding: 0 0 10px 30px;
        @include sp {
          padding: 0 0 8px 20px;
        }
      }
    }
  }

  &__item {
    padding: 15px;
    border-top: 1px solid #D5D5D5;
    @include sp {
      padding: 10px 8px;
    }

    label.radio > span {
      color: $primary;
    }
  }

  &__canvas-arrows {
    position: relative;
  }

  &__canvas-arrow {
    position: absolute;
    bottom: 0;
    z-index: 100;
    left: -15px;
    background-color: #EEEEEE;
    border: 1px solid #D5D5D5;
    width: 15px;
    height: 15px;
    background: {
      image: url('../img/icons/icon-arrow-left.svg');
      size: 3px auto;
      position: center;
      repeat: no-repeat;
    }

    &--right {
      right: -15px;
      left: auto;
      background: {
        image: url('../img/icons/icon-arrow-right.svg');
        size: 3px auto;
        position: center;
        repeat: no-repeat;
      }
    }

    &--vertical {
      top: -15px;
      bottom: auto;
      right: 0;
      left: auto;
      transform: rotate(90deg);
    }

    &--bottom {
      top: auto;
      bottom: -15px;
      transform: rotate(-90deg);
    }
  }

  &__canvas-wrapper {
    overflow-x: auto;
    margin-bottom: 20px;

    &::-webkit-scrollbar {
      height: 15px;
    }

    &::-webkit-scrollbar-track {
      background: $white;
      border: 1px solid #D5D5D5;
      border-left: 0;
      border-right: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: #EEEEEE; 
      border: 1px solid #D5D5D5;
      margin-left: 15px;
    }

    &--no-margin {
      margin-bottom: 0;
    }
  }

  &__canvas-inner {
    height: 500px;
    margin-bottom: 20px;
    @include sp {
      height: 200px;
    }

    &--small {
      height: 300px;
      margin-bottom: 0;

      canvas {
        margin-bottom: 0;
      }
    }
  }
}
