.display {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  width: 1080px;
  max-width: 90%;
  margin: 0 auto;
  z-index: 500;
  display: flex;
  flex-direction: column;
  @include sp {
    top: 50px;
  }

  &__content {
    width: 100%;
    height: 40.47px;
    position: relative;
    z-index: 3;
    padding-right: 22px;
    background: {
      image: url('../img/icons/icon-arrow-down.svg');
      size: 13.85px auto;
      repeat: no-repeat;
      position: right 40px center;
    }
    @include center;
  }

  &__icon {
    margin-right: 10px;
  }

  &__button {
    display: block;
    position: relative;
    height: 41.47px;
    width: 220px;
    background-color: transparent;
    outline: none;
    border: 0;
    font-size: 16px;
    margin-left: auto;
    order: 1;
    filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.05));

    &::before {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 190px;
      height: 0;
      border-top: 40.47px solid $white;
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      border-bottom: 0;
    }

    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: -1px;
      width: 192px;
      height: 0;
      border-top: 41.47px solid #D5D5D5;
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      border-bottom: 0;
    }

    &.toggle {
      color: $white;

      &::before {
        border-top: 40.47px solid #333;
      }

      .display__icon {
        filter: brightness(0) invert(1);
      }

      .display__content {
        background: {
          image: url('../img/icons/icon-arrow-up.svg');
          size: 13.85px auto;
          repeat: no-repeat;
          position: right 40px center;
        }
      }
    }
  }

  &__inner-button {
    .button {
      margin: 20px auto 0;
    }
  }

  &__container {
    width: 100%;

    &.hide {
      display: none;
    }
  }

  &__main-content {
    width: 100%;
    padding: 38px 20px 40px;
    background-color: $white;
    border: 1px solid #D5D5D5;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  }

  &__inner {
    @include center;
    @include sp {
      flex-direction: column;
    }
  }

  &__block {
    position: relative;
    @include sp {
      width: 100%;
    }

    &:first-child {
      margin-right: 20px;
      @include sp {
        margin-right: 0;
      }
    }

    &:last-child {
      margin-left: 20px;
      @include sp {
        margin-left: 0;
      }
    }
  }

  &__multiselect {
    width: 320px;
    height: 449px;
    background-color: #FAFAFA;
    border: 1px solid #D5D5D5;
    padding: 22px 40px 28px;
    max-width: 100%;
    @media screen and (max-width: 1050px) {
      width: 295px;
    }
    @include sp {
      width: 100%;
      padding: 20px 10px;
    }

    label {
      display: block;
      user-select: none;
    }

    label.disabled {
      pointer-events: none;

      &.selected {
        pointer-events: visible;
      }
    }
  }

  &__title {
    font-size: 16px;
    margin-bottom: 12px;
  }

  &__togglers {
    @include sp {
      margin: 10px 0;
    }
  }
}
