.progress {
  &__list {
    margin-bottom: 80px;
  }

  &__item {
    display: flex;

    & > span {
      width: 127px;
      flex-shrink: 0;
      @include sp {
        width: 20%;
      }
    }
  }

  &__content {
    width: 870px;
    padding: 0 0 19px 40px;
    position: relative;
    word-break: break-word;
    @include sp {
      width: 80%;
      padding: 0 0 20px 20px;
    }

    &::before {
      content: '';
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: #D5D5D5;
      position: absolute;
      top: 5px;
      left: -6.5px;
      @include sp {
        top: 6px;
      }
    }

    &::after {
      content: '';
      width: 1px;
      height: 100%;
      background-color: #D5D5D5;
      position: absolute;
      top: 5px;
      left: 0;
      @include sp {
        top: 6px;
      }
    }

    &.no-paddingb {
      padding-bottom: 0;
    }
  }

  &__text {
    margin-top: 15px;
    @include sp {
      margin-top: 10px;
    }
  }
}
