.table {
  @include max-width;

  .blue-color {
    cursor: pointer;
  }

  &.full {
    width: 100%;
    max-width: 100%;

    .table__container {
      width: 100%;
      @include sp {
        width: 1080px;
      }
    }

    .table__th,
    .table__td {
      padding: 10px 15px;
    }

    .table__th {
      font: {
        family: $hiragino;
        size: 14px;
      }
    }

    .table__td {
      font: {
        family: $hiragino;
        size: 12px;
      }
    }

    .table__td:nth-child(2) {
      text-align: center;
    }

    .table__td:nth-child(3) {
      text-align: right;
    }

    .table__wrapper {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      white-space: normal;

      &.space-between {
        justify-content: space-between;
      }

      &.center {
        justify-content: center;
      }
    }

    .button {
      margin: 5px 10px 5px 0;
    }
  }

  &--full {
    width: 100%;
    max-width: 100%;

    .table__container {
      width: 100%;
      @include sp {
        width: 1080px;
      }
    }
  }

  &--m40 {
    margin-top: 40px;
  }

  &__scroll {
    position: relative;
    width: 100%;
    overflow: auto;

    &--mobile {
      overflow: visible;
      @include sp {
        overflow: auto;
      }
    }

    &.margin-top {
      margin-top: 20px;
    }

    &.hide {
      display: none;
    }

    &--blue {
      &::-webkit-scrollbar {
        width: 6px;
      }
  
      &::-webkit-scrollbar-track {
        background: #E0E0E0;
        border-radius: 100px;
      }
  
      &::-webkit-scrollbar-thumb {
        background: #004B9E;
        border-radius: 100px;
      }
    }

    &--h290 {
      height: 290px;
    }

    &--h900 {
      height: 900px;
    }
  }

  &__container {
    width: 1080px;
    background-color: $white;
    border-radius: 4px;
    border: 1px solid #eee;
    font-family: $hiragino;
    border-collapse: collapse;

    &.full {
      width: 100%;
    }

    &--list {
      border-left: 0;
      border-right: 0;
      border-top: 1px solid #D5D5D5;
      border-bottom: 1px solid #D5D5D5;
    }

    &--w1000 {
      width: 1000px !important;
    }

    &--w984 {
      width: 984px !important;
    }

    &--w500 {
      width: 500px !important;
    }

    &--w440 {
      width: 440px !important;
      display: block;
      margin: 0 auto;
    }

    &--sp-w500 {
      @include sp {
        width: 500px !important;
      }
    }

    &--equal {
      table-layout: fixed;
    }

    &--no-border {
      border: 0;

      .table__td {
        border: 0;
      }
    }

    &--gray-border {
      border: 1px solid #D5D5D5;

      .table__th,
      .table__td {
        border: 1px solid #D5D5D5;
      }
    }

    &.no-border {
      border: 1px solid #eee;

      .table__th:first-child,
      .table__td:first-child {
        border-right: 1px solid #eee;
      }

      .table__th,
      .table__td {
        border-left: 0;
        border-right: 0;
      }
    }
  }

  &__tr,
  &__td {
    .flex {
      display: flex;
      align-items: center;

      & > .question {
        margin-left: 10px;
      }
    }

    .flex-center {
      display: flex;
      justify-content: center !important;
      align-items: center;

      & > .question {
        margin-left: 10px;
      }
    }
  }

  &__thead {
    background-color: #fafafa;

    &--big {
      .table__th {
        font-size: 19px;
        @include sp {
          font-size: 12px;
        }
      }
    }

    &--blue {
      background-color: #F5FAFF;
    }

    &--bordered-blue {
      background-color: $white;

      .table__th {
        border: 0;
        border-bottom: 1px solid #005BAB;
        color: #005BAB;
      }
    }
  }

  &__tbody {
    &--bg {
      .table__tr:nth-child(even) {
        background-color: #F5FAFF !important;
      }
    }

    &--big {
      .table__td {
        font-size: 19px;
        @include sp {
          font-size: 12px
        }
      }

      .table__tr:nth-child(even) {
        background-color: #F5FAFF !important;
      }
    }

    .table__tr:nth-child(even) {
      background-color: $white;
    }

    &.padding-sm {
      & > .table__tr > .table__td {
        padding: 5px 10px !important;
      }
    }
  }

  &__sort {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
    text-align: left;
    padding: 13px 0;

    &::after {
      content: '';
      display: inline-block;
      margin-left: 5.5px;
      width: 14px;
      height: 14px;
      flex-shrink: 0;
      background: {
        image: url('../img/table/arrow-down-table.svg');
        size: 100%;
        repeat: no-repeat;
      }
    }

    &--ml10 {
      &::after {
        margin-left: 10px;
      }
    }

    &.descending {
      &::after {
        background: {
          image: url('../img/table/arrow-up-table.svg');
          size: 100%;
          repeat: no-repeat;
        }
      }
    }

    &--no-padding {
      padding: 0;
    }

    &--disabled {
      &::after {
        opacity: 0.3;
      }
    }

    &--white {
      &.descending {
        &::after {
          background: {
            image: url('../img/icons/arrow-down-table-white.svg');
            size: 100%;
            repeat: no-repeat;
          }
        }
      }
    }

    &--gray {
      &::after {
        background: {
          image: url('../img/icons/arrow-up-gray.svg');
          size: 100%;
          repeat: no-repeat;
        }
      }
    }
  }

  &__th,
  &__td {
    border: 1px solid #eee;
    font-size: 10px;
    line-height: getem(10, 14);

    &--center {
      text-align: center !important;
    }

    &--right {
      text-align: right;
    }

    &--blue {
      background-color: #2B6DB2;
      color: $white;
      height: 40px;
    }

    &--orange {
      background-color: #EFAC3C;
      color: $white;
    }
  }

  &__th {
    .question {
      margin-left: 6px;
    }

    &--f12 {
      font-size: 12px;
      padding: 17px 0;
    }

    &--p10 {
      padding: 10px 0;
    }

    &--p11 {
      padding: 11px 0;
    }

    &--p17 {
      padding: 17px 0;
    }

    &--f14 {
      font-size: 14px;
    }

    &--flex {
      @include center;
    }

    &.no-border {
      border: 0;
    }
  }

  &__td {
    word-break: break-all;
    padding: 13px 8px 13px 10px;

    &.no-border-right {
      border-right: none;
    }

    &.no-border-left {
      border-left: none;
    }

    &.height {
      height: 54px;
    }

    &.bg-gray {
      background-color: #fafafa;
    }

    &.bg-blue {
      background-color: #f5faff;
    }

    &.bg-yellow {
      background-color: #fffde3;
    }

    &.relative {
      position: relative;
    }

    &--f12 {
      font-size: 12px;
      padding: 12px 10px;
    }

    &--f14 {
      font-size: 14px;
      padding: 6px 23px;
    }

    &--center {
      text-align: center;
    }

    &--right {
      text-align: right;
    }

    &--p14 {
      padding: 12px 14px;
    }

    &--no-padding {
      padding: 0;
    }

    &--center {
      text-align: center;
    }

    &--p8p6p {
      padding: 8px 6px;
    }

    &--p10 {
      padding: 10px;
    }

    &--p20 {
      padding: 20px;
    }

    &--p710 {
      padding: 7px 10px;
    }

    &--p17 {
      padding: 17px 10px;
    }

    &:first-child {
      white-space: nowrap;
    }
  }

  &__td-wrap {
    display: flex;
    align-items: center;

    & > img {
      margin-right: 3px;
    }

    &--col {
      flex-direction: column;
    }
  }

  &__tr {
    transition: 0.3s ease;

    &:nth-child(even) {
      background-color: #f5faff;
    }

    &--white {
      background-color: $white !important;
    }
  }

  &__label {
    font: {
      family: $hiragino;
      size: 16px;
    }
    margin-bottom: 20px;

    &.no-margin {
      @include sp {
        margin-bottom: 20px !important;
      }
    }

    &--pr20 {
      padding-right: 20px;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

    &.column {
      @include sp {
        flex-direction: column;
      }
    }

    .table__td {
      font-size: 12px !important;
      white-space: normal !important;
    }

    .center {
      justify-content: center;
    }

    .no-margin-b {
      margin-bottom: 0;
    }
  }

  &__column {
    &:not(:last-child) {
      margin-right: 20px;
      @include sp {
        margin: 0 0 20px 0;
      }
    }

    &--md {
      max-width: 410px;
      width: 100%;
      @include sp {
        max-width: 100%;
      }
    }

    &--sm {
      max-width: 140px;
      width: 100%;
      @include sp {
        max-width: 100%;
      }
    }
  }

  &__caption {
    display: block;
    text-align: right;
    font-size: 12px;
    font-family: $hiragino;
    margin-top: 9px;
  }

  &__text {
    font-size: 20px;

    &--small {
      font-size: 16px;
    }
  }

  &__button-delete {
    background-color: transparent;
    width: 13.88px;
    height: 16px;
    outline: none;
    border: 0;
    cursor: pointer;
    background: {
      image: url('../img/icons/icon-trash.svg');
      size: 100%;
      repeat: no-repeat;
    }
  }
}
