.login {
  position: relative;

  &__header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 13px 20px 13px 30px;
    background-color: #222C34;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 999;
    transition: 0.3s ease;
    @include sp {
      padding: 0;
    }

    &--main {
      padding: 45px 100px;
      background-color: transparent;
      @include mx1200 {
        padding: 45px 20px;
      }
      @include sp {
        padding: 0;
      }
    }

    &.active {
      background-color: #222C34;
      padding: 13px 20px 13px 30px;
      @include sp {
        padding: 0;
      }
    }
  }

  &__logo-container {
    display: flex;
    align-items: center;
  }

  &__logo-text {
    font-family: $lato;
    font-weight: $bold;
    font-size: 20px;
    border-left: 1px solid $white;
    padding-left: 40px;
    margin-left: 40px;
    color: $white;
    letter-spacing: 0.2em;
    line-height: 1.5em;
    @include sp {
      font-size: 16px;
      padding-left: 20px;
      margin-left: 20px;
    }
  }

  &__header-logo {
    &--main {
      width: 192.53px;
      @include sp {
        width: 144.4px;
      }
    }
  }

  &__navigation {
    font-family: $lato;
    font-weight: $bold;
    font-size: 14px;
    color: $white;
    display: flex;
    align-items: center;
    @include sp {
      position: absolute;
      top: 50px;
      background-color: #222C34;
      left: 0;
      right: 0;
      display: none;
    }

    &.active {
      @include sp {
        display: block;
      }
    }
  }

  &__nav-item {
    margin: 0 20px;
    text-transform: uppercase;
    @include sp {
      margin: 0;
      width: 100%;
      margin: 0;
      border-top: 1px solid rgba(238, 238, 238, 0.2);
    }
  }

  &__nav-text {
    letter-spacing: 0.2em;
    @include sp {
      display: block;
      width: 100%;
      height: 100%;
      padding: 15px;
    }
  }

  &__button {
    width: 200px;
    height: 40px;
    font-family: $noto-sans;
    font-weight: $bold;
    font-size: 14px;
    color: $white;
    background: linear-gradient(to right, #005BAB, #18915A);
    border-radius: 25px;
    @include center;

    &--header {
      width: 140px;
      height: 34px;
      font-family: $lato;
      letter-spacing: 0.2em;
      @include sp {
        width: calc(100% - 30px);
        margin: 10px auto;
      }
    }

    &--center {
      margin: 0 auto;
    }
  }

  &__header-menu {
    @include sp {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 15px;
      height: 50px;
    }

    &--main {
      @include sp {
        box-shadow: none;
      }
    }
  }

  &__container {
    position: relative;
    z-index: 1;
    height: 100vh;
    min-height: 768px;
    background: {
      image: url('../img/login/mainvisual.jpg');
      size: cover;
      repeat: no-repeat;
      position: center;
    }
    @include sp {
      min-height: 100vh;
      height: auto;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.2);
      z-index: -1;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 1130px;
    max-width: 90%;
    margin: 0 auto;
    height: 100%;
    @include sp {
      padding-top: 100px;
      flex-direction: column;
    }
  }

  &__info {
    color: $white;
    @media screen and (max-width: 1130px) {
      margin-right: 2%;
    }
    @include sp {
      margin: 0 0 60px;
      order: 1;
    }
  }

  &__info-title {
    font-size: 50px;
    font-family: $lato;
    letter-spacing: 0.1em;
    margin-bottom: 18px;
    @include sp {
      font-size: 30px;
    }
  }

  &__info-text {
    font-family: $lato;
    font-size: 16px;
    font-weight: $light;
    letter-spacing: 0.1em;
    line-height: getem(16, 24);
    margin-bottom: 43px;
    width: 540px;
    @media screen and (max-width: 1130px) {
      width: auto;
    }
    @include sp {
      font-size: 14px;
      margin-bottom: 33px;
    }
  }

  &__inner {
    width: 440px;
    padding: 44px 40px 38px;
    background-color: $white;
    border-radius: 10px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    flex-shrink: 0;
    @include sp {
      width: 100%;
      margin-bottom: 30px;
      padding: 20px;
    }
  }

  &__title {
    font-family: $lato;
    font-size: 20px;
    letter-spacing: 0.2em;
    margin-bottom: 38px;
    text-align: center;
  }

  &__form {
    margin-bottom: 20px;
  }

  &__form-item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__forgot-container {
    font-size: 12px;
    font-family: $noto-sans;
    margin-top: 18px; 
    text-align: center;
  }

  &__external {
    margin-top: 28px;
    padding-top: 24px;
    border-top: 1px solid $black;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__external-link {
    font-family: $noto-sans;
    font-size: 14px;
    font-weight: $bold;
    padding-right: 20px;
    background: {
      image: url('../img/icons/icon-arrow-right-2.svg');
      repeat: no-repeat;
      position: right top 6px;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}
