.popup {
  display: none;
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);

  &--show {
    display: block;
  }

  &.t75 {
    top: -75px;
  }

  &.w300 {
    width: 300px;
  }

  &.w160 {
    width: 160px;
  }

  &__wrap {
    background-color: #005BAB;
    border: 1px solid $white;
    border-radius: 4px;
    padding: 10px;
    position: relative;
    @include center;

    &--col {
      flex-direction: column;
    }

    &::before {
      content: '';
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 9px 7.5px 0 7.5px;
      border-color: #005bab transparent transparent transparent;
      position: absolute;
      left: 50%;
      bottom: -9px;
      transform: translate(-50%, -50%);
      z-index: 1;
    }

    span,
    a {
      color: $white;
    }

    a:not(:last-child) {
      margin-bottom: 5px;
    }
  }
}
