.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 85px 0 100px;
  font-family: $noto-sans;
  font-size: 14px;
  margin-top: auto;
  margin-bottom: 38px;
  font-weight: $light;
  @media screen and (max-width: 1080px) {
    max-width: 95%;
    padding: 0;
    margin: auto auto 38px;
  }
  @include sp {
    flex-direction: column;
  }

  &__copyright {
    @include sp {
      margin-bottom: 10px;
    }
  }

  &__links {
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
    }
  }

  &__link {
    margin: 0 15px;
    @include sp {
      margin: 10px 0;
    }
  }

  &--main {
    position: absolute;
    margin: 0 auto;
    bottom: 38px;
    left: 0;
    right: 0;
    color: $white;
    z-index: 1;
    @include sp {
      position: static;
      padding-bottom: 38px;
    }
  }
}
