.breadcrumbs {
  margin-bottom: 30px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 1230px;
    width: 100%;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 30px;
    @include sp {
      padding: 0 10px;
    }
  }

  &__item {
    font-size: 12px;
    position: relative;
    padding-right: 10px;

    a {
      transition: 0.3s ease;
      &:hover {
        opacity: 0.8;
      }
    }

    &:not(:last-child) {
      margin-right: 15px;

      &::before {
        content: '';
        width: 4px;
        height: 8px;
        background: {
          image: url('../img/icons/icon-caret.svg');
          size: contain;
          repeat: no-repeat;
        };
        position: absolute;
        top: 50%;
        right: -5px;
        transform: translateY(-50%);
      }
    }

    span.gray {
      color: #9B9B9B;
    }
  }
}
