.d-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 900;
  height: 70px;
  background-color: $white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  padding: 0 40px 0 20px;
  @include mx1200 {
    padding: 0 10px 0 0;
    justify-content: center;
  }
  @include sp {
    height: 50px;
    padding: 0 15px;
    justify-content: space-between;
  }

  &__logo {
    @include mx1200 {
      width: 100px;
    }
    @include sp {
      width: auto;
    }
  }

  &__menu {
    width: auto;
    height: 100%;
    @include mx1200 {
      margin: 0 20px;
    }
    @include sp {
      margin: 0;
    }
  }

  &__wrap {
    height: 100%;
    @include center;
    @include sp {
      width: 100%;
      height: auto;
      justify-content: space-between;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    font-family: $noto-sans;
    height: 100%;
    @include sp {
      background-color: #222C34;
      color: $white;
      height: auto;
      display: block;
    }
  }

  &__link {
    position: relative;
    font-size: 14px;
    padding: 0 8px 10px;
    margin: 0 3px;
    @include mx1200 {
      font-size: 11px;
      padding: 0 4px 6px;
    }
    @include sp {
      display: block;
      width: 100%;
      font-size: 14px;
      padding: 15px 10px;
    }

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: #31362F;
      border-radius: 3px;
      transform: scale(0);
      transition: 0.3s ease;
      @include sp {
        display: none;
      }
    }

    &--sub {
      @include sp {
        width: auto !important;
      }
    }

    &--active {
      font-weight: $bold;

      &::before {
        transform: scale(1);
      }
    }
  }

  &__item {
    position: relative;
    height: 100%;
    cursor: pointer;
    @include center;
    @include sp {
      height: auto;
      width: 100%;
      border-bottom: 1px solid rgba(238, 238, 238, 0.2);
      display: block;
      overflow: hidden;
    }

    &:hover {
      .d-header__link::before {
        transform: scale(1);
      }
    }

    &.active {
      img {
        transform: rotate(0);
      }
    }
  }

  &__notif {
    display: flex;
    align-items: center;
    @include sp {
      width: 100%;
      justify-content: flex-end;
      padding-right: 20px;
    }
  }

  &__notif-item {
    position: relative;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__notif-icon {
    height: 20px;
  }

  &__notif-number {
    position: absolute;
    top: -7px;
    left: 7px;
    background-color: #F5A623;
    padding: 0 3px;
    border-radius: 2px;
    color: $white;
    font-size: 9px;
    font-weight: $bold;

    &--message {
      left: 12px;
    }
  }

  &__sublist {
    position: absolute;
    top: 70px;
    left: 0;
    width: 200px;
    padding: 10px 20px;
    background-color: #31362F;
    display: none;
    @include sp {
      position: static;
      width: 100%;
    }

    &.active {
      display: block;
    }
  }

  &__sublink {
    display: block;
    color: $white;
    font-family: $noto-sans;
    font-weight: $bold;
    font-size: 12px;
    line-height: getem(12, 30);
  }
}
