.subcontent {
  width: 100%;
  padding: 20px 20px 40px;
  background-color: #FAFAFA;
  border: 1px solid #D5D5D5;
  margin-top: 40.8px;
  @include sp {
    padding: 10px 10px 20px;
  }

  &__title {
    display: flex;
    align-items: center;

    h3 {
      font-size: 14px;
      font-weight: $semibold;
      margin-right: 10px;
    }
  }

  &__draggable {
    width: 100%;
    display: flex;
    margin-top: 21px;
    border: 1px solid #D5D5D5;
    background-color: $white;
    @include sp {
      margin-top: 11px;
      flex-direction: column;
    }
  }

  &__label {
    align-items: flex-start;
    padding: 16px 17px 16px 19px;
    flex-shrink: 0;
  }

  &__button-group {
    display: flex;
    align-items: center;
    margin-left: 22px;
  }

  &__button {
    width: 15px;
    height: 15px;
    background-color: #333;
    border-radius: 50%;
    border: 0;
    background: {
      image: url('../img/icons/icon-plus.png');
      position: center;
      repeat: no-repeat;
      size: 7px 7px;
    }

    &--close {
      width: 14px;
      height: 14px;
      border: 1px solid #333;
      box-sizing: content-box;
      background: {
        image: url('../img/icons/icon-close-white.svg');
        position: center;
        repeat: no-repeat;
        size: 6px 6px;
      }
    }

    &:not(:last-child) {
      margin-right: 10px;
    }

    &.hide {
      display: none;
    }
  }

  &__form-wrap {
    padding: 20px;
    width: calc(100% - 160px);
    @include sp {
      width: 100%;
    }
  }

  &__radio {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-left: 24px;
    @include sp {
      margin: 10px 0 0;
    }
  }

  &__label-inner {
    display: flex;
    align-items: center;
  }

  &__add {
    .button {
      margin: 20px 0 0 auto;
    }
  }

  &__shareholder-add {
    margin-top: 10px;

    button {
      margin-left: 127px;
      @include sp {
        margin-left: 0;
      }
    }

    &--mt10 {
      margin-top: 10px;
    }
  }

  &__candidate {
    display: none;

    &.active {
      display: block;
      margin-top: 20px;
      @include sp {
        margin-top: 10px;
      }
    }

    &--image {
      &.active {
        margin-top: 0;
      }
    }
  }

  &__main-button {
    margin-top: 39px;
    display: flex;
    justify-content: center;
    @include sp {
      flex-direction: column;
      margin-top: 20px;
    }

    .button {
      margin: 0 10px;
      @include sp {
        margin: 0 0 10px;
      }

      &:last-child {
        @include sp {
          margin-bottom: 0;
        }
      }
    }
  }
}
