html {
  font-family: $noto-sans;
  color: $gray;
  background-color: #f4f4f5;
  font-size: 10px;
  font-feature-settings: 'palt';
  @media screen and (max-width: 980px) {
    min-width: 980px;
  }
  @include sp {
    min-width: 100%;
  }
}

body {
  padding-top: 130px;
  min-height: 100vh;
  @include sp {
    padding-top: 80px;
  }

  &.login {
    padding-top: 88px;
    display: flex;
    flex-direction: column;
    @include sp {
      padding-top: 80px;
    }

    &--pt96 {
      padding-top: 96px;
      @include sp {
        padding-top: 90px;
      }
    }
  }

  &.no-padding {
    padding-top: 0;
    display: flex;
    flex-direction: column;
  }

  &.d-page {
    padding-top: 85px;
    @include sp {
      padding-top: 65px;
    }
  }
}

input[type=number] {
  appearance: textfield;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    appearance: none;
    margin: 0;
  }
}

button {
  cursor: pointer;
}

input {
  font-size: 14px;
}

.row {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  @include sp {
    flex-direction: column;
  }

  &.flex-start {
    justify-content: flex-start;
  }

  &.center {
    align-items: center;
    @include sp {
      align-items: flex-start;
    }
  }

  &.mb20 {
    margin-bottom: 20px !important;
  }

  &--sp-flex {
    @include sp {
      flex-direction: row;
    }
  }
}

.column {
  @include sp {
    width: 100%;
  }

  &.margin > label.radio > span {
    margin: 0 40px 15px 0;
  }
}

select,
textarea,
input {
  font-family: $hiragino;
}

.member {
  width: 50px;
  height: 30px;
  border: 1px solid #FF334B;
  border-radius: 4px;
  @include center;
  color: #FF334B;

  &--non {
    border: 1px solid #999;
    color: #999;
  }
}

.kinds {
  width: 45px;
  height: 30px;
  border-radius: 4px;
  color: $white;
  @include center;

  &.green {
    background-color: #6CB121;
  }

  &.olive {
    background-color: #3C670B;
  }

  &.light-blue {
    background-color: #36B2EF;
  }

  &.blue {
    background-color: #4394FF;
  }

  &.orange {
    background-color: #FF9300;
  }

  &.purple {
    background-color: #8653D8;
    border: 1px solid #5F0AFF;
  }
}
