.pagination {
  display: flex;
  align-items: center;

  &--big {
    .pagination__number {
      width: 48px;
      height: 48px;
      font-size: 20px;
      @include sp {
        width: 36px;
        height: 36px;
        font-size: 14px;
      }
    }
  }

  &--full {
    @include sp {
      justify-content: center;
      width: 100%;
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    font-family: $hiragino;
    @include sp {
      flex-direction: column;
      align-items: center;
    }

    &--top {
      margin: 0 0 20px;
    }

    &--bottom {
      margin-bottom: 20px;
    }
  }

  &__number {
    width: 36px;
    height: 36px;
    font-size: 14px;
    background-color: #4a4a4a;
    color: $white;
    border-radius: 4px;
    margin: 0 4.25px;
    @include center;

    &:first-child {
      margin-left: 0;
    }

    &.active {
      background-color: #005bab;
    }
  }

  &__text {
    font-size: 14px;
    letter-spacing: 0.8em;
    margin: 0 5px 0 10px;

    &--total {
      letter-spacing: normal;
      margin: 0 15px 0 20px;
    }
  }

  &__input-container {
    display: flex;
    align-items: center;
    @include sp {
      margin-top: 15px;
    }
  }

  &__nextprev {
    cursor: pointer;
    width: 36px;
    height: 36px;
    border: 0;
    margin: 0 5px;
    background: {
      image: url('../img/table/arrow-button-right.svg');
      size: 100%;
      repeat: no-repeat;
    }

    &--previous {
      transform: rotate(180deg);
    }

    &.disabled {
      opacity: 0.3;
      pointer-events: none;
    }
  }

  &__input {
    width: 60px;
    height: 36px;
    text-align: center;
    color: $black;
    font-size: 14px;
    border-radius: 4px;
    border: 1px solid #d5d5d5;

    &::placeholder {
      color: #999;
    }
  }

  &__result {
    display: block;
    font-size: 14px;
    margin-bottom: 17px;
    font-family: $hiragino;
  }
}
