.voting {
  border: 1px solid #000;

  &__form-item {
    width: 100%;
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &--start {
      align-items: flex-start;

      .voting__form-label {
        padding-top: 15px;
        @include sp {
          padding-top: 0;
        }
      }
    }

    &.mb44 {
      margin-bottom: 44.5px;
    }
  }

  &__form-label {
    width: 240px;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    @include sp {
      width: 100%;
      margin-bottom: 10px;
    }

    span {
      font: {
        family: $hiragino;
        size: 14px;
        weight: $bold;
      }
      line-height: 14px;
    }

    &.sm {
      width: 160px;
      @include sp {
        width: 100%;
      }
    }

    .question {
      margin-left: 10px;
    }

    &--column {
      flex-direction: column;
      align-items: flex-start;
      padding-top: 10px;
    }

    &--small {
      width: 218px;
      @include sp {
        width: 100%;
      }
    }

    &--smaller {
      width: 128px;
      @include sp {
        width: 100%;
      }
    }

    &--20P {
      width: 20%;
      @include sp {
        width: 100%;
      }
    }
  }

  &__form-input-wrap {
    width: 760px;
    display: flex;
    flex-wrap: wrap;
    max-width: calc(100% - 240px);
    @include sp {
      width: 100%;
      max-width: 100%;
    }

    &.margin {
      label.checkbox > span,
      label.radio > span {
        margin-right: 20px;
        @include sp {
          margin: 5px 20px 5px 0;
        }
      }
    }

    &.column {
      display: inline-block;

      .button.x-small {
        margin-top: 10px !important;
      }
    }

    .input__text {
      height: 40px;
    }

    &--auto {
      width: auto;
      @include sp {
        width: 100%;
      }
    }

    &--small {
      flex-wrap: nowrap;
      align-items: center;

      .input__text {
        width: 740px;
        flex-shrink: 0;
        max-width: 100%;
        @include sp {
          width: 100%;
        }
      }

      button {
        flex-shrink: 0;
      }
    }

    &--flex {
      flex-wrap: nowrap;
      align-items: center;
      @include sp {
        flex-wrap: wrap;
      }

      .button {
        margin-right: 0;
        @include mx1200 {
          margin-left: 10px;
        }
        @include sp {
          margin-left: 17px;
        }
        @media screen and (max-width: 480px) {
          margin-top: 10px;
        }
      }
    }
  }

  &__form-label-block {
    display: flex;
    align-items: center;

    &:first-child {
      margin-bottom: 16px;
    }
  }

  &__form-search {
    min-width: 110px;
    height: 25px;
    background-color: #333;
    color: $white;
    font-size: 12px;
    text-align: center;
    margin-top: 8px;
    border-radius: 4px;
    padding-left: 16px;
    border: 0;
    padding-bottom: 1px;
    background: {
      image: url('../img/icons/icon-search.svg');
      repeat: no-repeat;
      position: left 10px bottom 7px;
    }
  }

  &__form-block {
    position: relative;
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
      align-items: flex-start;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    .voting__form-item {
      margin-bottom: 0;

      button {
        margin-left: 17px;
        @include mx1200 {
          margin-left: 10px;
        }
        @include sp {
          margin-left: 0;
          position: absolute;
          top: 0;
          right: 0;
        }
      }
    }
  }

  &__form-add {
    margin: 10px 0 20px 240px;
    @include sp {
      margin-left: 0;
    }

    button {
      margin-left: 0;
    }
  }

  &__form-outer {
    .form__select-duplicate {
      margin-bottom: 20px;
    }
  }
}
