.topic {
  &__row {
    display: flex;
    justify-content: space-between;
    @include sp {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  &__column {
    width: 480px;
    @include sp {
      width: 100%;
    }

    &:not(:last-child) {
      @include sp {
        margin-bottom: 40px;
      }
    }

    &--full {
      width: 100%;
    }
  }

  &__heading {
    width: 100%;
    height: auto;
    min-height: 50px;
    border: 1px solid #D5D5D5;
    background-color: #005BAB;
    display: flex;
    align-items: center;
    padding: 0 20px;
    font-family: $hiragino;
    color: $white;
    margin-bottom: 20px;

    &--gradient {
      background: linear-gradient(90deg, rgba(0,91,171,1) 0%, rgba(24,145,90,1) 100%);
    }
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include sp {
      flex-direction: column;
      align-items: flex-start;;
    }

    & > span {
      flex-shrink: 0;
    }
  }

  &__content {
    margin-left: 55px;
    padding-left: 40px;
    border-left: 1px solid #D5D5D5;
    width: 100%;
    @include sp {
      margin: 10px 0 0;
      padding: 10px 0 0;
      border-top: 1px solid #D5D5D5;
      border-left: 0;
    }

    h6 {
      margin-bottom: 20px;
    }

    p {
      font-family: $hiragino;
      font-size: 14px;
    }
  }

  &__table {
    & > table {
      border: none;

      & .table__th {
        font-size: 16px;
        color: #005BAB;
        border: none !important;
        background-color: $white;
        border-bottom: 1px solid #005BAB !important;
      }

      & > .table__tbody > .table__tr:nth-child(even) {
        background-color: #F5FAFF !important;
      }

      & > .table__tbody > .table__tr > .table__td {
        border: none;
        font-size: 14px;
      }

      & > .table__tbody > .table__tr > .table__td:nth-child(1),
      & > .table__tbody > .table__tr > .table__td:nth-child(3) {
        text-align: center;
      }
    }
  }

  &__note {
    font-family: $hiragino;
    font-size: 12px;
  }

  &__list {
    margin-bottom: 80px;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }

  &__label-text {
    width: 100%;
    border-bottom: 1px solid #D5D5D5;
    padding-bottom: 12px;
    margin-bottom: 2px;

    label {
      font: {
        family: $hiragino;
        size: 16px;
        weight: $bold;
      }
      line-height: getem(16,16);
    }
  }

  &__field-row {
    display: flex;
    @include sp {
      flex-direction: column;
    }
  }

  &__field-text {
    display: block;
    white-space: nowrap;
    font-weight: $bold;
    font-size: 16px;
    line-height: getem(16, 16);
    padding: 0 20px;
    user-select: none;
    @include sp {
      padding: 10px 0;
    }

    &.read {
      color: #333;
    }
  }

  &__field-item {
    position: relative;
    margin: 10px 0;
    cursor: pointer;
    @include sp {
      margin: 0;
    }

    &:first-child {
      .topic__field-text {
        padding-left: 0;
      }
    }

    &:last-child {
      .topic__field-text {
        padding-right: 0;
      }
    }

    &:not(:last-child) {
      .topic__field-text {
        border-right: 1px solid #333;
        @include sp {
          border-right: 0;
          border-bottom: 1px solid #333;
        }
      }
    }
  }

  &__subfield {
    padding-top: 15px;
    position: absolute;
    z-index: 1;
    right: 50%;
    transform: translateX(50%);
    top: 18px;
    @include sp {
      position: relative;
      transform: unset;
      top: 0;
      right: 0;
    }

    &::before {
      content: '';
      position: absolute;
      right: 0;
      left: 0;
      margin: 0 auto;
      top: -7.5px;
      width: 49.45px;
      height: 23px;
      background: {
        image: url('../img/icons/triangle-top.svg');
        size: 100%;
        repeat: no-repeat;
      }
    }

    &.hide {
      display: none;
    }
  }

  &__subfield-inner {
    width: 400px;
    padding: 40px;
    background-color: #F4F4F5;
    border: 1px solid #D5D5D5;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    @include sp {
      width: 100%;
      padding: 20px;
    }
  }

  &__field-title {
    font-size: 20px;
    letter-spacing: 0.02em;
    padding-bottom: 15px;
    line-height: 1em;
  }

  &__subfield-list {
    height: 352px;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: #E0E0E0;
      border-radius: 100px;
    }

    &::-webkit-scrollbar-thumb {
      background: #004B9E;
      border-radius: 100px;
    }
  }

  &__subfield-title {
    display: block;
    font-size: 14px;
    font-weight: $medium;
    padding: 15px 20px;
    border-top: 1px solid #D5D5D5;
    transition: 0.3s ease;
    margin-right: 18px;

    &:last-child {
      border-bottom: 1px solid #D5D5D5;
    }

    &:hover {
      background-color: #005BAB;
      color: $white;
    }
  }
}
