.search {
  &--modal {
    padding: 60px 40px;
    overflow: hidden;
    @include sp {
      padding: 20px 5%;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include sp {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__title {
    font: {
      size: 20px;
      weight: $bold;
    }
  }

  &__list {
    @include sp {
      margin-top: 20px;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    @include sp {
      flex-direction: column;
      align-items: flex-start;

    }

    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }

  &__label {
    font: {
      family: $hiragino;
      size: 12px;
    }
    color: #555;
    width: 125px;
    @include sp {
      width: auto;
      margin-bottom: 10px;
    }
  }

  &__input-wrap {
    display: flex;
    align-items: center;
    @include sp {
      margin-bottom: 5px;
    }

    &:not(:last-child) {
      margin-right: 20px;
    }

    img {
      margin-left: 5px;
    }
  }
}

.search-by {
  padding: 30px 0;
  margin-top: 20px;
  border-top: 1px solid #EEE;
  border-bottom: 1px solid #EEE;

  &__list,
  &__item {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    align-items: center;
    margin-bottom: 10px;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  &__label {
    display: flex;
    align-items: center;
    margin-right: 30px;

    span {
      font: {
        family: $hiragino;
        size: 14px;
      }
    }

    img {
      width: 14px;
      height: 14px;
      margin-left: 10px;
    }
  }

  &__input-wrap {
    position: relative;
    display: flex;

    input {
      margin: 0 !important;
    }

    .input__text.sm {
      width: 90px;
      padding-left: 0;
      background-color: $white;
    }

    .button.blue {
      margin: 0 10px;
      @include sp {
        margin: 0 5px;
      }
    }

    .search {
      padding-left: 37.7px;
    }

    &.full {
      width: 100%;
    }
  }

  &__button {
    position: absolute;
    left: 15px;
    top: 8.5px;
    background-color: transparent;
    border: 0;
  }

  &__numbers {
    width: 860px;
    max-width: 100%;
    margin: 10px 0 0 auto;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    background-color: #fafafa;
    border: 1px solid #d5d5d5;
    padding: 10px;
    height: 110px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: #E0E0E0;
    }

    &::-webkit-scrollbar-thumb {
      background: #004B9E;
      border-radius: 3px;
    }

    &--full {
      width: 100%;
    }

    &--small {
      height: 80.5px;
      padding: 9px 10px;
      border-radius: 4px;
      margin-top: 19px;
    }
  }

  &__number-item {
    min-width: 100px;
    background-color: $gray;
    color: $white;
    border-radius: 10px;
    padding: 2px 18px 2px 10px;
    position: relative;
    margin: 0 10px 10px 0;

    &--text {
      display: flex;
      align-items: center;
      padding: 3px 26px 5px 10px;
      border-radius: 20px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__number-text {
    &.f12 {
      font-size: 12px;
      margin: 0;
      width: auto;
      @include sp {
        word-wrap: break-word;
        width: 100%;
        margin: 0 !important;
      }
    }
  }

  &__number-close {
    background-color: transparent;
    border: 0;
    position: absolute;
    right: 10px;
  }
}
