.button {
  min-width: 160px;
  height: 40px;
  border-radius: 4px;
  appearance: none;
  border: 0;
  font-size: 14px;
  color: $white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 20px;
  padding: 0 15px;
  transition: 0.3s;
  @include sp {
    min-width: 140px;
  }

  &.hiragino {
    font-family: $hiragino;
  }

  &.p20 {
    padding: 0 20px;
  }

  &.zindex {
    position: relative;
    z-index: 300;
  }

  &:hover {
    opacity: 0.8;
  }

  &.hide {
    display: none;
  }

  &.show {
    display: block;
  }

  &.mw150 {
    min-width: 150px;
    @include sp {
      min-width: 130px;
    }
  }

  &.mw165 {
    min-width: 165px;
  }

  &.mw190 {
    min-width: 194px;
    @include sp {
      min-width: 174px;
    }
  }

  &.w100 {
    min-width: 100px;
  }

  &.w135 {
    min-width: 135px;
  }

  &.w147 {
    min-width: 147px;
  }
  
  &.w240 {
    min-width: 240px;
  }

  &.w230 {
    min-width: 230px;
    @include sp {
      min-width: 50%;
    }
  }

  &.w200 {
    min-width: 200px;
  }

  &.w320 {
    width: 320px;
    min-width: unset;
    margin: 0 auto;
    max-width: 100%;
  }

  &.w210 {
    min-width: 210px;
    height: 50px;
    @include sp {
      min-width: 140px;
      height: 30px;
    }
  }

  &.w180 {
    min-width: 180px;
  }

  &.w103 {
    min-width: 103px;
    background: {
      image: url('../img/icons/icon-arrow-rounded.svg');
      size: 6px auto;
      repeat: no-repeat;
      position: right 15.4px center;
    }
  }

  &.w118 {
    min-width: 118px;
  }

  &.w270 {
    min-width: 270px;
  }

  &.w70 {
    min-width: 70px;
  }

  &.h50 {
    height: 50px;
    @include sp {
      height: 30px;
    }
  }

  &.h40 {
    height: 40px;
  }

  &.h24 {
    height: 24px;
  }

  &.large {
    min-width: 200px;
    @include sp {
      min-width: 140px;
    }
  }

  &.x-small {
    min-width: 80px;
    padding: 0 10px;
    margin: 0 !important;
    cursor: pointer;

    img {
      margin-right: 10px;
    }
  }

  &.small {
    min-width: 140px;
    text-align: center;
  }

  &.rounded {
    height: 30px;
    border-radius: 15px !important;
  }

  &.square {
    min-width: 40px;
    padding: 0;
    @include center;

    img {
      width: 18px;
      height: 18px;
      margin: 0;
    }
  }

  &.outline {
    min-width: 90px;
    height: 22px;
    border: 1px solid #555555;
    border-radius: 45px;
    padding-top: 3px;
    font: {
      family: $hiragino;
      size: 10px;
    }
    color: #555555;
    margin-right: 0
  }

  &.full {
    width: 100%;
    height: 50px;
    margin: 0;
  }

  &.center {
    @include center;
  }

  &.blue {
    background-color: #004B9E;
  }

  &.white {
    background-color: $white;
    color: #333;
  }

  &.medium-blue {
    background-color: #005BAB;
  }

  &.gray {
    background-color: #333333;
    color: $white;
  }

  &.tundora {
    background-color: $tundora;
  }

  &.shadowed-steel {
    background-color: #4A4A4A;
  }

  &.disabled {
    background-color: #D5D5D5;
    pointer-events: none;
  }

  &.light-gray {
    background-color: #9B9B9B;
  }

  &.dark-gray {
    background-color: #333;
  }

  &.yellow {
    background-color: #F5A623;
  }

  &.sm {
    width: auto;
    min-width: unset;
    padding: 0;
    background-color: transparent;
    border-radius: 0;
    height: auto;
    margin: 0;
  }

  &.delete {
    width: 100%;
  }

  &.icon {
    min-width: 30px !important;
    width: 30px;
    height: 30px;
    justify-content: center;

    img {
      margin-right: 0;
    }
  }

  &.arrow-right {
    position: relative;
    max-width: 70px;
    min-width: 70px;
    height: 23px;
    background-color: #005BAB;
    padding: 5px;
    font-size: 8px;
    box-sizing: border-box;

    img.xs {
      width: 3px;
      height: 6px;
      margin: 0 0 0 8px;
    }
  }

  &.auto {
    padding: 1px 10px 0 10px;
    margin: 0 0 0 10px;
    flex-shrink: 0;
    min-width: auto;
    margin: 0 0 0 10px;
    @include sp {
      margin: 0;
    }
  }

  &.smaller {
    width: 80px;
    min-width: auto;
  }

  &.no-margin {
    margin: 0;
  }

  &.margin-sm {
    margin: 0 10px;
  }

  &.mr10 {
    margin-right: 10px !important;
  }

  &.p10 {
    padding: 0 10px;
  }

  &.absolute {
    position: absolute;
    top: -40px;
    right: 0;
    @include sp {
      top: 5px;
    }
  }

  &.reset {
    background-color: transparent;
    color: $gray;
    font-family: $hiragino;
    font-size: 14px;
    letter-spacing: 0.04em;
    min-width: unset;
    cursor: pointer;
  }

  &.h30 {
    height: 30px;
  }

  &.f11 {
    font-size: 11px;
  }

  &.has-text {
    height: 28px;
    padding: 0 10px;

    img {
      width: 12px;
      height: 12px;
    }

    span {
      margin: 0 0 0 4.5px;
    }
  }

  img {
    width: 14px;
    height: 14px;
    margin-right: 5px;
  }

  &--delivery-add-toggle {
    border-radius: 50px;
    width: 100%;
    height: 30px;
  }

  &--delivery-add-toggle {
    position: relative;
    margin: 0;
    width: 100%;
    min-width: 0;
    padding-left: 0;
    background-color: $tundora !important;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: url('../img/icons/icon-arrow-right-white.svg') !important;
      background-position: 80% 50% !important;
      background-repeat: no-repeat !important;
      background-size: 20% !important;
      @include sp {
        transform: rotate(90deg);
        top: 0;
        bottom: 0;
        left: auto;
        right: 15px;
        width: 11px;
        background-size: auto !important;
        background-position: auto !important;
      }
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &.remove {
      padding-left: 40%;

      &::before {
        background-position: 20% 50% !important;
        background-image: url('../img/icons/icon-arrow-left-white.svg') !important;
        @include sp {
          left: 15px;
          right: auto;
          background-size: auto !important;
          background-position: auto !important;
        }
      }
    }
  }

  &--add {
    position: relative;
    width: 128px;
    height: 30px;
    max-width: 100%;
    border-radius: 18px;
    margin: 0 0 10px 0 !important;
    min-width: unset;
    padding-left: 0;
    padding-right: 15px;
    font-family: $hiragino;

    &::after {
      content: '';
      position: absolute;
      width: 12.36px;
      height: 100%;
      right: 14.6px;
      background: {
        image: url('../img/icons/icon-arrow-right-white.svg');
        position: center;
        repeat: no-repeat;
        size: 100% auto;
      }
      @include sp {
        transform: rotate(90deg);
      }
    }

    &.remove {
      padding-right: 0;
      padding-left: 15px;

      &::after {
        right: auto;
        left: 16px;
        transform: rotate(180deg);
        @include sp {
          transform: rotate(-90deg);
        }
      }
    }
  }

  &--add-all {
    margin: 0 0 30px 0 !important;

    &.remove {
      margin: 6px 0 0 0 !important;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: center;
    margin-top: 70px;

    &.space-between {
      margin-top: 0;
      justify-content: space-between;
      align-items: center;

      label {
        font: {
          family: $hiragino;
          size: 14px;
        }
      }
    }

    &.wrap-sp {
      @include sp {
        flex-direction: column;
      }

      label {
        @include sp {
          margin-bottom: 10px;
        }
      }
    }

    &.mt40 {
      margin-top: 40px;
    }

    &.mt35 {
      margin-top: 35px;
    }

    &.mb35 {
      margin-bottom: 35px;
    }

    &.mt15 {
      margin-top: 15px;
    }

    &.mb20 {
      margin: 0 0 20px;
    }

    &.sp-mb15 {
      @include sp {
        margin-bottom: 15px;
      }
    }
  }

  &__text {
    margin-right: 30px;

    span {
      font-size: 12px;
      color: #005BAB;
    }
  }

  &__group {
    display: flex;

    &--center {
      align-items: center;
    }
  }

  &__content-wrap {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: none;
    padding-bottom: 6px;

    &.active {
      display: block;
    }

    &--big {
      left: auto;
      transform: unset;
      right: 0;
      @include sp {
        left: 50%;
        transform: translateX(-50%);
        right: auto;
      }
    }

    &--bottom {
      top: 30px;
      padding-top: 8px;
      padding-bottom: 0;
    }
  }

  &__content-links {
    width: 140px;
    padding: 12px 0;
    border-radius: 4px;
    border: 1px solid $white;
    background-color: #005BAB;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font: {
      family: $hiragino;
      size: 12px;
    };
    color: $white;
    line-height: 20px;
    position: relative;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    @include sp {
      font-size: 10px;
      line-height: normal;
    }

    &::before {
      content: '';
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 9px 7.5px 0 7.5px;
      border-color: #005bab transparent transparent transparent;
      position: absolute;
      left: 50%;
      bottom: -9px;
      transform: translate(-50%, -50%);
      z-index: 1;
    }

    &::after {
      content: '';
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 6.5px 5.5px 0 5.5px;
      border-color: $white transparent transparent transparent;
      position: absolute;
      left: 50%;
      bottom: -10px;
      transform: translate(-50%, -50%);
    }

    a:hover {
      font-weight: $bold;
      transition: 0.3s ease;
    }

    &--w1169 {
      width: 1169.95px;
      font-size: 20px;
      font-weight: $semibold;
      line-height: getem(20, 32);
      padding: 40px;
      @include sp {
        width: 344px;
        font-size: 10px;
        line-height: normal;
        padding: 10px;
      }

      &::before {
        content: '';
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 13px 12.5px 0 12.5px;
        border-color: #005bab transparent transparent transparent;
        position: absolute;
        left: auto;
        right: 16%;
        bottom: -15px;
        z-index: 1;
        @include sp {
          border-width: 9px 7.5px 0 7.5px;
          bottom: -9px;
        }
      }
  
      &::after {
        content: '';
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 10.5px 12.5px 0 12.5px;
        border-color: $white transparent transparent transparent;
        position: absolute;
        left: auto;
        right: 16%;
        bottom: -16px;
        @include sp {
          border-width: 6.5px 5.5px 0 5.5px;
          bottom: -10px;
        }
      }
    }

    &--w164 {
      width: 164px;
      @include sp {
        font-size: 12px;
        line-height: 20px;
      }

      &::before {
        content: '';
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 10px 10px 10px;
        border-color: transparent transparent #005BAB transparent;
        position: absolute;
        left: auto;
        right: 36%;
        bottom: auto;
        top: -3px;
        z-index: 1;
      }
  
      &::after {
        content: '';
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 10px 6px 10px;
        border-color: transparent transparent $white transparent;
        position: absolute;
        left: auto;
        right: 36%;
        bottom: auto;
        top: -6px;
      }
    }

    &--w301 {
      width: 301px;
      @include sp {
        font-size: 12px;
        line-height: 20px;
      }
    }
  }
}
