.news {
  margin-bottom: 80px;

  &__item {
    display: flex;
    @include sp {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  &__date {
    font-size: 14px;
    width: 128px;
    flex-shrink: 0;
    margin-right: 40px;
    padding-right: 40px;
    border-right: 1px solid #D5D5D5;
    @include sp {
      width: auto;
      margin: 0 0 10px;
      padding-right: 0;
      border: 0;
    }
  }

  &__title {
    font-size: 18px;
    margin-bottom: 15px;
    color: #005BAB;
  }

  &__text {
    font-size: 14px;
    line-height: getem(14, 24);
  }
}
