.text {
  font: {
    family: $hiragino;
    size: 16px;
    weight: $medium;
  }

  &.f14 {
    font-size: 14px;
    line-height: getem(14, 24);
  }

  &.center {
    text-align: center;
  }

  &.medium {
    font-weight: $medium;
  }

  a {
    color: #005BAB;
  }

  &__wrapper {
    &:not(:last-child) {
      margin-bottom: 90px;
      @include sp {
        margin-bottom: 60px;
      }
    }
  
    h6,
    p,
    span {
      font: {
        family: $hiragino;
      }
    }
  
    h6 {
      font-size: 16px;
      border-bottom: 1px solid #d5d5d5;
      margin-bottom: 30px;
      padding-bottom: 10px;
      @include sp {
        margin-bottom: 20px;
      }
    }
  
    p,
    span {
      font-size: 14px;
      line-height: 24px;
    }

    .f16 {
      font-size: 16px;
    }
  }

  &__wrapper-list {
    p.margin-bottom {
      margin-bottom: 35px;
      @include sp {
        margin-bottom: 20px;
      }
    }
  }

  &__wrapper-item {
    &:not(:last-child) {
      margin-bottom: 80px;
      @include sp {
        margin-bottom: 40px;
      }
    }
  }

  &__bordered-content {
    width: 100%;
    height: 220px;
    border: 1px solid #d5d5d5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    padding: 20px;
    box-sizing: border-box;
    @include sp {
      margin-top: 40px;
    }

    &.sm {
      max-width: 400px;
      width: 100%;
      height: 80px;
      padding: 20px;
      margin: 5px auto 20px;
    }

    span.blue-color {
      margin-top: 3px;
    }
  
    &--p20 {
      height: auto;
      padding: 20px;
      margin-top: 0;
      @include sp {
        padding: 15px;
      }
    }
  }


  &__row {
    display: flex;
    justify-content: space-between;
    margin: 40px 0 63px;
    @include sp {
      flex-direction: column;
      margin: 20px 0;
    }
  }

  &__col {
    :first-child {
      margin-right: 40px;
      @include sp {
        margin: 0 0 20px 0;
      }
    }

    img {
      width: auto;
      height: auto;
      @include sp {
        max-width: 100%;
        margin-left: auto !important;
        margin-right: auto !important;
        display: block;
      }
    }
  }

  &__gray-box {
    width: 800px;
    max-width: 100%;
    font-size: 12px;
    line-height: getem(12, 22);
    padding: 15px 20px;
    background-color: #F4F4F4;
    text-align: center;
    margin-top: 10px;
  }

  &__note {
    display: flex;
    justify-content: flex-end;
  }
}
