.questionnaire {
  &__header {
    display: flex;
    justify-content: space-between;

    .button {
      margin-right: 0;
      @include sp {
        height: auto;
      }
    }
  }

  &__title {
    font-size: 16px;
  }

  &__list {
    margin-top: 20px;
  }

  &__block {
    border: 1px solid #D5D5D5;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__block-header {
    display: flex;
    padding: 15px 20px;
    font-size: 14px;
    font-weight: $bold;
    background-color: #FAFAFA;
    border-bottom: 1px solid #D5D5D5;
  }

  &__number {
    margin-right: 23px;
  }

  &__content {
    padding: 40px;
  }

  &__table-item {
    display: flex;
    padding: 10px 0;
    font-size: 12px;

    &:nth-child(even) {
      background-color: #F5FAFF;
    }
  }

  &__table-label {
    font-weight: $bold;
    width: 130px;
    text-align: center;
    flex-shrink: 0;
    @include sp {
      width: 90px;
    }
  }

  &__table-info {
    padding-right: 28px;
    line-height: getem(12, 20);
  }
}
