.select {
  width: 80px;
  height: 30px;
  border: 1px solid #D5D5D5;
  border-radius: 4px;
  font: {
    family: $hiragino;
    size: 16px;
  }
  padding-left: 15px;
  appearance: none;
  background: {
    image: url('../img/icons/icon-select.svg');
    repeat: no-repeat;
    position: right 10.4px center;
  }
  outline-color: #d5d5d5;
  @include sp {
    font-size: 14px;
  }

  &::-ms-expand {
    display: none;
  }

  &.disabled {
    pointer-events: none;
    color: #c1c1c1;
  }

  &--gray {
    color: #c1c1c1;
  }

  &--wide {
    width: 740px;
    @include sp {
      width: 100%;
    }
  }

  &--big {
    height: 40px;
    color: #333;
  }

  &--blue {
    color: $primary;
    background-color: #F5FAFF;
    border: 1px solid $primary;

    option {
      color: $primary;
    }

    &.light-blue {
      color: #acc5e2;
    }
  }

  &--md {
    width: 200px;
  }

  &--large {
    width: 100%;
    max-width: 400px;
  }

  &--w110 {
    width: 110px;
    font-size: 12px;
  }

  &--larger {
    width: 100%;
    max-width: 521.57px;
    font-size: 17px;
  }

  &--w300 {
    width: 300px;
  }

  &--w260 {
    width: 260px;
  }

  &--w95 {
    width: 95.68px;
    font-size: 12px;
  }

  &--full {
    width: 100%;
  }

  &__wrap {
    span {
      font: {
        family: $hiragino;
        size: 14px;
      }
      margin: 0 10px;
    }

    &--full-width {
      width: 100%;
    }

    &--flex {
      display: flex;
      align-items: center;
    }
  }

  option {
    color: #333;
  }
}
