// CHECKBOX

[type="checkbox"] {
  display: none;
}

[type="checkbox"]:checked + span,
[type="checkbox"]:not(:checked) + span {
  position: relative;
  display: inline-block;
  cursor: pointer;
  font: {
    family: $hiragino;
    size: 14px;
  }
  color: $gray;
  padding-left: 30px;
}

[type="checkbox"]:checked + span:before,
[type="checkbox"]:not(:checked) + span:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 1px solid #D5D5D5;
  border-radius: 4px;
  background: $white;
}

[type="checkbox"]:checked + span::after,
[type="checkbox"]:not(:checked) + span::after {
  content: '';
  width: 14.59px;
  height: 10.55px;
  background: {
    image: url('../img/icons/icon-checkmark.svg');
    size: 100%;
    repeat: no-repeat;
  }
  position: absolute;
  top: 5px;
  left: 3px;
}

[type="checkbox"]:not(:checked) + span::after {
  opacity: 0;
  transform: scale(0);
}

[type="checkbox"]:checked + span::after {
  opacity: 1;
  transform: scale(1);
}

.checkbox {
  &--big {
    [type="checkbox"]:checked + span,
    [type="checkbox"]:not(:checked) + span {
      padding-left: 40px;
    }

    [type="checkbox"]:checked + span:before,
    [type="checkbox"]:not(:checked) + span:before {
      content: '';
      width: 25px;
      height: 25px;
    }

    [type="checkbox"]:checked + span:after,
    [type="checkbox"]:not(:checked) + span:after {
      width: 20px;
      height: 20px;
      top: 6px;
      left: 4px;
    }
  }

  &.p25 {
    span {
      padding-left: 30px;
    }
  }

  &.p20 {
    span {
      padding-left: 20px;
    }
  }

  &.large {
    span {
      font-size: 16px !important;
      line-height: 16px;
    }
  }

  &.small {
    span {
      font-size: 12px !important;
    }
  }

  &.disabled {
    pointer-events: none;
    color: #D5D5D5;
  }

  &.nobutton {
    margin: 10px 0;
    white-space: nowrap;
    @include sp {
      white-space: normal;
    }

    span {
      font-weight: $bold;
      color: #333;
      padding: 0 20px 0 18px !important;
      margin: 0;
      @include sp {
        padding: 0 10px !important;
      }

      &::before,
      &::after {
        display: none;
      }
    }

    &:first-child {
      span {
        padding-left: 0 !important;
        @include sp {
          padding-left: 10px !important;
        }
      }
    }

    &:not(:last-child) {
      border-right: 1px solid rgb(190, 166, 166) !important;
    }

    [type="checkbox"]:checked + span {
      color: #005BAB;
    }
  }
}

// RADIO
.radio {
  &.large {
    span {
      font-size: 14px !important;
      line-height: 14px;
      @include sp {
        line-height: unset;
      }
    }
  }

  &.margin-sm > span {
    margin-right: 25px !important;
    padding-top: 1px;
  }

  &.nobutton {
    span {
      font-weight: $bold;
      padding: 0 10px !important;
      margin: 0;

      &::before,
      &::after {
        display: none;
      }
    }

    &:first-child {
      span {
        padding-left: 0 !important;
      }
    }

    &:not(:last-child) {
      border-right: 1px solid #333;
    }

    [type="radio"]:checked + span {
      color: #005BAB;
    }
  }
}

[type="radio"]:checked,
[type="radio"]:not(:checked) {
  display: none;
}

[type="radio"]:checked + span,
[type="radio"]:not(:checked) + span {
  position: relative;
  display: inline-block;
  cursor: pointer;
  font: {
    family: $hiragino;
    size: 12px;
  }
  color: $gray;
  padding-left: 30px;
}

[type="radio"]:checked + span::before,
[type="radio"]:not(:checked) + span::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border: 1px solid #D5D5D5;
  background: $white;
  border-radius: 50%;
}

[type="radio"]:checked + span::after,
[type="radio"]:not(:checked) + span::after {
  content: '';
  position: absolute;
  left: 4px;
  top: 50%;
  transform: translateY(-50%);
  width: 14px;
  height: 14px;
  background: #005BAB;
  border-radius: 50%;
  transition: 0.3s ease;
}

[type="radio"]:not(:checked) + span::after,
[type="checkbox"]:not(:checked) + span::after {
  opacity: 0;
}

[type="radio"]:checked + span::after,
[type="checkbox"]:checked + span::after {
  opacity: 1;
}
