.white {
  color: $white;
}

.white-bg {
  background: $white;
}

.black {
  color: $black;
}

.black-bg {
  background: $black;
}

.primary {
  color: $primary;
}

.primary-bg {
  background: $primary;
}

.iris-blue-bg {
  background-color: $iris-blue;
}

.gamboge-bg {
  background-color: $gamboge;
}

.radical-red-bg {
  background-color: $radical-red;
}

.lima-bg {
  background-color: $lima;
}

.electric-indigo-bg {
  background-color: $electric-indigo;
}

.blue-color {
  color: $blue;
}

.red-color {
  color: $red;
}

.gray {
  color: #C1C1C1;
}

.dark-gray {
  color: #333;
}

.bg-gray {
  background-color: #fafafa;
}

.overflow {
  overflow: hidden;
}

.content {
  padding-bottom: 100px;
  overflow: hidden;
  @include sp {
    padding-bottom: 80px;
  }

  &--auto {
    overflow: visible;
  }
}

.sm {
  font-size: 12px;
}

.md {
  font-size: 14px;
}

.light-gray {
  color: #D5D5D5;
  font-weight: $regular;
}

.container {
  position: relative;
  @include max-width;
  background-color: $white;
  padding: 30px 40px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  margin-top: 25px;
  @include sp {
    padding: 20px 5%;
  }

  &:not(:nth-last-child(2)) {
    margin-bottom: 60px;
  }

  &:nth-last-child(2) {
    margin-bottom: 40px;
  }

  &--wide {
    max-width: 100%;
    width: 1440px;
  }

  &--PTB40PXLR2P {
    padding: 40px 2.04%;
  }

  &--p40 {
    padding: 40px;
    @include sp {
      padding: 20px 5%;
    }
  }

  &--p60 {
    padding: 55px;
    @include sp {
      padding: 30px 5%;
    }
  }

  &--m40 {
    margin-bottom: 40px;
  }

  &--pb80 {
    padding-bottom: 80px;
  }

  &--mt35 {
    margin-top: 35px;
  }

  &--mt40 {
    margin-top: 40px;
  }

  &--mt20 {
    margin-top: 20px;
  }

  &--pb100 {
    padding-bottom: 100px;
    @include sp {
      padding-bottom: 50px;
    }
  }

  &--mb20 {
    margin-bottom: 20px !important;
  }

  &__flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 18px;
    @include sp {
      flex-direction: column;
      align-items: flex-start;
    }

    h6 {
      margin-bottom: 0;
    }

    &--flex-start {
      justify-content: flex-start;
      @include sp {
        flex-direction: row;
      }
    }

    &--flex-end {
      justify-content: flex-end;
    }

    .button {
      @include sp {
        margin-right: 0 !important;
      }
    }

    .form__counter {
      margin-top: 10px;
    }
  }

  &.hiragino {
    font-family: $hiragino;
  }
}

.regular {
  font-weight: $regular;
}

.bold {
  font-weight: $bold;
}

.pc {
  @include sp {
    display: none;
  }
}

.sp {
  @media screen and (min-width: 768px) {
    display: none;
  }
}

.question {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  &--margin {
    margin-top: 4px;
  }

  &--margin-top {
    margin-top: -22px;
  }

  &.hide {
    display: none;
  }
}

.pointer,
.js-details {
  cursor: pointer;
}

.no-padding {
  padding: 0;
}

.js-candidate-forms .js-delete-content {
  @include sp {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.no-margin {
  margin: 0 !important;
}

.no-margin-left {
  margin-left: 0 !important;
}

.no-margin-bottom {
  margin-bottom: 0 !important;
}

.js-auto-resize {
  overflow-y: hidden;
}

.newline {
  flex-shrink: 1 !important;
}

.light-small {
  font-weight: $regular !important;
  font-size: 12px !important;
}

.ml10 {
  margin-left: 10px;
  @include sp {
    margin: 10px 0 0;
  }
}

.ml40 {
  margin-left: 40px;
}

.ml7 {
  margin-left: 7px;
}

.mb10 {
  margin-bottom: 10px !important;
}

.has-border {
  border-top: 1px solid #D5D5D5;
}

.text-fs21 {
  font-size: 21px !important;
  font-weight: $medium;
  @include sp {
    font-size: 14px !important;
  }
}

.text-fs20 {
  font-size: 20px !important;
  font-weight: $medium;
  @include sp {
    font-size: 14px !important;
  }
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-right-pc {
  text-align: right;
  @include sp {
    text-align: left;
  }
}

.js-button-hover {
  display: flex;
}

.f14 {
  font-size: 14px;
  line-height: 24px;
}

.f16 {
  font-size: 16px;
  line-height: 26px;
}

.f18 {
  font-size: 18px;
}

.ml-10 {
  margin-left: -10px;
}

.fw500 {
  font-weight: $medium;
}

.wrapper {
  &:not(:last-child) {
    margin-bottom: 50px;
  }
}

.error-text {
  font: {
    family: $hiragino;
    size: 16px;
    weight: 500;
  }
  color: #E60000;
  text-align: center;
  padding: 0 50px;
  @include sp {
    padding: 0 15px;
  }
}

.sticky {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 2;
}

.mail__list {
  padding-top: 40px;
  margin-top: 40px;
  border-top: 1px solid #D5D5D5;
}

.mail__item {
  display: flex;

  span {
    white-space: nowrap;
    width: 70px;
  }

  p {
    margin-left: 10px;
  }
}

.no-border {
  border: 0;
}

.js-email-only {
  @include mx1200 {
    width: 42%;
  }
  @include sp {
    width: 100%;
  }
}

.gray {
  color: #999;
}

.no-overflow {
  overflow: visible;
  @include sp {
    overflow: auto;
  }
}

.draggable {
  position: absolute;
  right: 10px;
  top: 15px;
  width: 16px;
  height: 11px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  div {
    width: 100%;
    height: 1px;
    background-color: #D5D5D5;
  }

  &--list {
    right: 22px;
    top: 23px;
  }

  &--folder {
    right: 22px;
    top: 29px;
  }
}

.js-draggable {
  cursor: all-scroll;
}

.relative {
  position: relative;
}

.zindex5 {
  z-index: 5;
}

.ui-ghost {
  opacity: 0;
}

.js-target-draggable {
  user-select: none;
}

.hidden {
  display: none;
}
