.textarea {
  width: 100%;
  min-height: 200px;
  border: 1px solid #D5D5D5;
  border-radius: 4px;
  color: $gray;
  font: {
    family: $hiragino;
    size: 14px;
  }
  padding: 10px 0 0 15px;
  overflow: hidden;
  resize: vertical;

  &::placeholder {
    opacity: 0.3;
    color: #333;
  }

  &--large {
    min-height: 250px;
  }

  &--small {
    min-height: 30px;
    padding: 4px 0 0 15px;
  }


  &--h100 {
    min-height: 100px;
  }

  &--no-resize {
    resize: none;
  }

  &--w740 {
    width: 740px;
    max-width: 100%;
  }

  &.h117 {
    min-height: 117px;
  }
}
