.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 999;
  @include sp {
    background-color: $white;
  }

  &__container,
  &__list,
  &__sublist,
  &__secondary,
  &__primary {
    display: flex;
  }

  &__container {
    align-items: center;
    @include sp {
      justify-content: space-between;
      padding-right: 15px;
    }
  }

  &__logo {
    width: 100px;
    height: 100px;
    @include sp {
      width: getsp375(100px);
      height: getsp375(100px);
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__menu {
    width: calc(100% - 100px);
    @include sp {
      display: none;
    }

    &.active {
      @include sp {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: calc(var(--vh, 1vh) * 100);
        position: absolute;
        top: 50px;
        left: 0;
        overflow-y: auto;
        padding-bottom: 50px;
      }
    }
  }

  &__secondary,
  &__primary {
    align-items: center;
    color: $white;

    a {
      transition: 0.3s ease;

      &:hover {
        opacity: 0.5
      }
    }
  }

  &__secondary {
    height: 40px;
    justify-content: space-between;
    background-color: $tertiary;
    font-size: 12px;
    padding: 0 30px 0 50px;
    @include sp {
      height: auto;
      padding: 0;
    }
  }

  &__primary {
    height: 60px;
    background-color: $secondary;
    font-size: 14px;
    padding: 0 50px;
    @include sp {
      width: 100%;
      height: auto;
      padding: 0;
    }

    .header__list {
      height: 100%;
    }
  }

  &__list {
    @include sp {
      width: 100%;
      flex-direction: column;
    }
  }

  &__item {
    @include sp {
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: 30px;
      @include sp {
        margin-right: 0;
        border-bottom: 1px solid rgba(238, 238, 238, 0.2);
      }
    }

    &.dropdownSecondary {
      position: relative;
    }
    
    &.active {
      .header__wrap img {
        transform: rotate(0);
      }

      &.dropdownPrimary {
        .header__wrap {
          position: relative;
  
          &::before {
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 7.5px 10px 7.5px;
            border-color: transparent transparent #ffffff transparent;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
            @include sp {
              content: none;
            }
          }
        }
      }
    }

    a {
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      @include sp {
        padding: 15px 10px;
      }

      &.bold {
        font-weight: $bold;
      }

      & > span {
        font: {
          family: $hiragino;
          size: 8px;
        }
        width: 22px;
        height: 12px;
        background-color: #F5A623;
        border-radius: 2px;
        @include center;
        margin-left: 5px;
      }
    }
  }

  &__wrap {
    width: auto;
    height: 100%;
    @include sp {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: auto;
      width: 100%;
    }

    a {
      @include sp {
        width: 93%;
      }
    }

    img {
      width: 14px;
      height: 8px;
      transform: rotate(180deg);
      @include sp {
        margin-right: 10px;
      }
    }
  }

  &__sublist {
    width: 100%;
    height: auto;
    min-height: 50px;
    background-color: $white;
    align-items: center;
    position: absolute;
    top: 100px;
    left: 0;
    color: #999;
    display: none;
    flex-wrap: wrap;
    z-index: 1;
    padding: 10px 11%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    @include sp {
      height: auto;
      flex-direction: column;
      align-items: center !important;
      position: relative;
      top: 0;
      left: 0;
      transform: translateX(0);
      font-size: 12px;
      padding: 5px 20px;
      box-shadow: none;
    }

    a.bold {
      color: #333;
    }

    &--secondary {
      width: 140px;
      height: auto;
      padding: 5px 10px;
      background-color: #323A41;
      color: $white;
      flex-direction: column;
      align-items: flex-start;
      top: 29px;
      left: 50%;
      transform: translateX(-50%);
      @include sp {
        top: 0;
        width: 100%;
      }

      .header__item:not(:last-child) {
        margin-right: 0;
      }

      .header__item a {
        &:hover {
          color: $white !important;
          opacity: 0.8 !important;
        }
      }
    }

    &.active {
      display: flex;
    }

    .header__item a {
      width: 100%;
      padding: 5px 10px;
      transition: 0.3s ease;
      @include sp {
        padding: 10px;
      }

      &:hover {
        color: $gray;
        opacity: 1;
      }
    }
  }

  &__btn {
    @include sp {
      width: 100%;
      padding: 15px 10px;
      background-color: $primary;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      color: $white;
    }
  }

  &__hamburger {
    display: none;
    @include sp {
      flex-shrink: 0;
      width: 25px;
      height: 22px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &.close {
      span:nth-child(1) {
        transform: translate(0, 9px) rotate(45deg);
      }

      span:nth-child(2) {
        display: none;
      }

      span:nth-child(3) {
        transform: translate(0, -10px) rotate(-45deg);
      }
    }

    span {
      @include sp {
        width: 100%;
        height: 3px;
        background-color: $primary;
        border-radius: 1.5px;
      }
    }
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
