.home {
  @include max-width;
  margin-top: 48px;

  &__block {
    padding: 34px 40px 40px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    background-color: $white;
    @include sp {
      padding: 30px 20px;
      margin-bottom: 40px;
    }

    &.no-pb {
      padding-bottom: 0;
    }

    &:not(:last-child) {
      margin-bottom: 40px;
    }

    &--pb11 {
      padding-bottom: 11px;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: $hiragino;

    .button {
      margin-right: 0;
    }
  }

  &__title {
    font-size: 18px;
    font-feature-settings: normal;
  }

  &__divide {
    display: flex;
    justify-content: space-between;
    @include mx768 {
      flex-direction: column;
    }
  }

  &__divide-content {
    width: 48.15%;
    @include mx768 {
      width: 100%;
    }
  }

  &__events {
    margin-top: 15px;
    border-top: 1px solid #D5D5D5;
  }

  &__event-left {
    position: relative;
    padding-right: 20px;
    border-right: 1px solid #D5D5D5;
    flex-shrink: 0;
    width: 122px;
    margin-right: 20px;
    @include sp {
      width: auto;
      margin: 0 0 10px;
      border-right: 0;
      padding-right: 0;
    }
  }

  &__event-date {
    font-size: 10px;
  }

  &__event-new {
    display: block;
    font-size: 12px;
    font-weight: $bold;
    color: #FF334B;
  }

  &__event-title {
    font-size: 14px;
    color: #005BAB;
    word-break: break-word;

    &--black {
      color: #333;
    }
  }

  &__event-text {
    margin-top: 5px;
    font-size: 14px;
    line-height: getem(14, 24);
    word-break: break-word;

    &--title {
      padding-bottom: 5px;
      margin-bottom: 9px;
      border-bottom: 1px solid #D5D5D5;
    }
  }

  &__event-item {
    position: relative;
    margin-top: 20px;
    display: flex;
    @include sp {
      flex-direction: column;
    }

    &--news {
      margin-top: 0;
      @include sp {
        flex-direction: row;
      }

      &:first-child {
        margin-top: 20px;
      }

      .home__event-left {
        width: 96px;
        @include sp {
          border-right: 1px solid #D5D5D5;
          width: 70px;
          padding-right: 10px;
          margin: 0 10px 0 0;
        }

        &::after {
          content: '';
          position: absolute;
          top: 0;
          right: -5px;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background-color: #D5D5D5;
        }
      }

      .home__event-date {
        font-size: 12px;
        @include sp {
          font-size: 10px;
        }
      }

      .home__event-text {
        line-height: getem(14, 20);
        margin-bottom: 9px;
      }
    }

    &--information {
      margin-top: 14px;

      .home__event-left {
        width: 96px;
      }

      .home__event-date {
        font-size: 12px;
      }

      .home__event-text {
        line-height: getem(14, 20);
      }
    }
  }

  &__event-link {
    display: block;
    font-size: 14px;
    color: #005BAB;
  }

  &__event-subitem {
    display: flex;
    align-items: center;
  }

  &__event-time {
    width: 35px;
    text-align: right;
    margin-right: 10px;
  }

  &__monthly {
    margin-top: 30px;
  }

  &__monthly-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16.5px;
  }

  &__monthly-prevnext {
    position: relative;
    color: #0059A6;
    padding-left: 12px;
    font-size: 12px;
    font-weight: $bold;
    background: {
      image: url('../img/icons/icon-arrow-left-blue.svg');
      size: 6.5px 9px;
      repeat: no-repeat;
      position: left center;
    }

    &--next {
      padding: 0 12px 0 0;
      background: {
        image: url('../img/icons/icon-arrow-right-blue.svg');
        size: 6.5px 9px;
        repeat: no-repeat;
        position: right center;
      }
    }
  }

  &__monthly-date {
    font-weight: $bold;
    font-size: 16px;
    color: #0059A6;
  }

  &__monthly-item {
    padding: 12px 4px;
    border-bottom: 1px solid #D5D5D5;
    display: flex;
    margin-right: 20px;

    &:first-child {
      border-top: 1px solid #D5D5D5;
    }
  }

  &__monthly-month {
    margin-right: 22px;
    flex-shrink: 0;
    text-align: center;
    font-size: 12px;
    line-height: getem(12, 20);

    span {
      display: block;
    }
  }

  &__monthly-outer {
    position: relative;
  }

  &__monthly-content {
    height: 911px;
    overflow: auto;

    &--table {
      padding-right: 10px;

      & > .table {
        @include sp {
          overflow-x: auto;
        }
      }
    }

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: #E0E0E0;
      border-radius: 100px;
    }

    &::-webkit-scrollbar-thumb {
      background: #004B9E;
      border-radius: 100px;
    }
  }

  &__monthly-text {
    font-size: 14px;
    line-height: getem(14, 20);
  }

  &__text {
    font-size: 14px;
    font-weight: $bold;
  }

  &__detail {
    position: absolute;
    font-size: 14px;
    right: 17px;
    bottom: 12px;
    @include sp {
      position: static;
    }
  }

  &__market {
    margin-top: 23.7px;
  }

  &__market-block {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__market-header {
    position: relative;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    padding: 15px 30px 15px 20px;
    background-color: #005BAB;
    cursor: pointer;
    color: $white;
    margin-bottom: 3.8px;
    @include sp {
      flex-direction: column;
      height: auto;
      align-items: flex-start;
    }

    &::after {
      content: '';
      position: absolute;
      right: 17px;
      width: 11px;
      height: 6px;
      bottom: calc(50% - 2.5px);
      filter: brightness(0) invert(1);
      background: {
        image: url('../img/icons/icon-arrow-down-blue.svg');
        repeat: no-repeat;
        size: 100%;
      }
    }

    &.toggle {
      &::after {
        transform: rotate(180deg);
      }
    }
  }

  &__market-title {
    font-size: 16px;
    margin-right: 16px;
  }

  &__market-date {
    font-size: 12px;
  }

  &__stats {
    margin: 20.5px 0 15.5px;
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
    }
  }

  &__stats-item {
    width: 255px;
    height: 80px;
    display: flex;
    align-items: center;
    padding-left: 40px;
    border-right: 1px solid #D5D5D5;
    @include sp {
      padding-left: 0;
      justify-content: center;
      border-right: 0;
      border-bottom: 1px solid #D5D5D5;
      width: 100%;
    }

    &:first-child {
      width: 245px;
      padding-left: 30px;
      @include sp {
        padding-left: 0;
      }
    }

    &:last-child {
      width: 245px;
      padding-right: 0;
      border-right: 0;
      @include sp {
        border-bottom: 0;
      }
    }
  }

  &__stats-subtitle {
    font-weight: $bold;
    font-size: 12px;
    display: block;
  }

  &__stats-number {
    color: #0058A5;
    font-weight: $bold;
  }

  &__stats-number-text {
    font-size: 30px;
  }

  &__stats-text {
    font-size: 20px;
    margin-left: 5px;
  }

  &__stats-icon {
    opacity: 0.2;
    margin-left: 20px;
  }

  &__stats-note {
    display: block;
    text-align: right;
    font-size: 12px;
  }

  &__graph {
    margin-top: 15px;
  }

  &__table {
    margin-top: 24px;
  }

  &__line {
    margin-top: 20.8px;
  }

  &__text-only {
    font-size: 14px;
    line-height: getem(14, 20);
    margin-top: 14px;
    padding-top: 18.5px;
    border-top: 1px solid #D5D5D5;
  }
}
