.form {
  position: relative;
  padding-top: 35px;

  &__list-container {
    max-height: 0;
    overflow: hidden;
    transition: 0.3s ease;
    margin-bottom: 30px;
    @include sp {
      margin-bottom: 20px;
    }
  }

  &__list {
    border: 1px solid #D5D5D5;
    margin-top: 30px;
    @include sp {
      margin-top: 20px;
    }

    &--no-margin {
      margin: 0;
    }

    &--confirm {
      width: 498.31px;
      max-width: 100%;
      @include sp {
        margin-bottom: 20px;
      }
    }

    &--mb40 {
      margin-bottom: 40px;
    }

    &--no-mt {
      margin-top: 0;
    }

    &.mt40 {
      margin-top: 40px;
    }

    &.no-border-bottom {
      border-bottom: 0;
    }

    &.no-border-right {
      border-right: 0;
    }
  }

  &__item {
    width: 100%;
    height: auto;
    min-height: 50px;
    display: flex;
    @include sp {
      flex-direction: column;
      align-items: flex-start;
    }
    
    &:not(:last-child) {
      border-bottom: 1px solid #D5D5D5;
    }

    .checkbox {
      margin: 5px 0;
    }

    &--half {
      width: 50%;
      border-right: 1px solid #D5D5D5;
      @include sp {
        width: 100%;
      }
    }

    &--confirm {
      min-height: 40px;
      flex-direction: row;
      align-items: stretch;
    }

    &--mh40 {
      min-height: 40px;
    }

    &--no-border {
      border-bottom: 0 !important;
    }
  }

  &__label {
    width: 160px;
    background-color: #FAFAFA;
    padding-left: 20px;
    padding-right: 10px;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    font-weight: $bold;
    @media screen and (max-width: 1080px) {
      flex-shrink: 1;
    }
    @include sp {
      width: 100%;
      padding: 10px;
    }

    &--login {
      background-color: transparent;
      font-weight: $regular;
    }

    &--200 {
      width: 200px;
      @include sp {
        width: 100%;
      }
    }

    &--220 {
      width: 220px;
      padding: 10px 20px;
      @include sp {
        width: 100%;
      }

      span {
        line-height: getem(14, 20);
      }
    }

    &--240 {
      width: 240px;
      padding-right: 16px;
      @include sp {
        width: 100%;
      }

      span {
        line-height: getem(14, 20);
      }
    }

    &--w110 {
      width: 110px;
      @include sp {
        width: 100%;
      }
    }

    &--large {
      width: 225px;
      @include sp {
        width: 100%;
      }
    }

    &--wide {
      width: 184px;
      flex-shrink: 0;
      @include sp {
        width: 100%;
      }
    }

    &--flex-start {
      align-items: flex-start;
      padding-top: 20px;

      img {
        margin-top: 2px;
      }
    }

    &--flex {
      align-items: flex-start;
      padding-top: 4px;
    }

    &--border {
      border-right: 1px solid #D5D5D5;
      @include sp {
        border-right: 0;
        border-bottom: 1px solid #D5D5D5;
      }
    }

    &--white {
      width: 200px;
      background-color: transparent;
      padding: 5px 13px;
      @include sp {
        width: 35%;
        border-right: 1px solid #D5D5D5;
        flex-shrink: 0;
        border-bottom: 0;
      }
    }

    span {
      flex-shrink: 0;

      font: {
        family: $hiragino;
        size: 14px;
      }

      & > span.sm {
        font-size: 10px;
        line-height: 10px;
      }
    }

    img {
      width: 14px;
      height: 14px;
      margin-left: 10px;
    }

    &--auto {
      padding-top: 10px;
      padding-bottom: 10px;

      span {
        flex-shrink: 1;
      }
    }

    &--full {
      width: 100%;
      padding: 0;
      margin-bottom: 8px;
    }

    &--w194 {
      width: 194px;
      @include sp {
        width: 100%;
      }
    }

    &--required {
      display: flex;
      align-items: center;

      .red {
        margin-left: 12px;
        color: #E60000;
        font-size: 12px;
      }
    }
  }

  &__wrapper {
    max-height: 0;
    overflow: hidden;
    transition: 0.3s ease;
    
    &.display {
      max-height: 300px !important;
      overflow: visible;
      margin-top: 10px;
    }
  }

  &__input-wrap {
    width: calc(100% - 160px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 10px;
    @include sp {
      width: 100%;
      padding: 10px;
    }

    &.padding-big {
      padding: 10px 20px !important;
    }

    &.margin-left {
      margin-left: -10px !important;
    }

    &--wide {
      width: calc(100% - 184px);
      @include sp {
        width: 100%;
      }
    }

    &--confirm {
      padding: 5px 15px !important;
    }

    &--full {
      width: 100%;
      padding: 0;
    }

    &--center {
      justify-content: center;
    }

    span {
      font-size: 14px;
      margin: 0 10px;
      @include sp {
        margin: 0;
      }

      &.f12 {
        font-size: 12px;
        @include sp {
          margin: 10px 0 0 ;
        }
      }

      &.gray {
        opacity: 0.3;
        color: #333;
      }
    }

    .radio {
      span {
        @include sp {
          margin: 0 10px 5px;
        }
      }
    }

    .button {
      @include sp {
        margin: 10px 0 0;
      }
    }

    &.flex {
      flex-wrap: unset;

      .button {
        @include sp {
          margin: 0 0 5px 10px;
        }
      }
    }

    &.flex-sp {
      flex-wrap: unset;
      @include sp {
        flex-direction: column;
      }
    }

    &.not-checkbox {
      padding: 5px 10px 5px 20px;
      @include sp {
        padding: 10px;
      }
    }
  }

  &__input-row {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include sp {
      flex-wrap: wrap;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    span {
      width: 130px;
      flex-shrink: 0;
      @include sp {
        width: 100%;
      }
    }

    span.auto {
      width: auto;
      margin: 0 10px;
      @include sp {
        margin: 0 10px 0 0;
      }
    }
    
    .input__text {
      @include sp {
        margin: 10px 0;
      }
    }

    &--start {
      align-items: flex-start;
    }

    &.mb24 {
      margin-bottom: 24px;
    }
  }

  &__pull {
    border-bottom: 1px solid #EEE;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    transition: 0.3s ease;

    &:hover {
      opacity: 0.8;
    }

    span {
      font: {
        family: $hiragino;
        size: 12px;
      }
      color: $gray;
      position: relative;
      width: 200px;
      height: 24px;
      @include center;

      &::before {
        content: '';
        width: 1px;
        height: 24px;
        background-color: #EEE;
        position: absolute;
        top: 0;
        left: -2px;
        transform: rotate(-10deg);
      }

      &::after {
        content: '';
        width: 1px;
        height: 24px;
        background-color: #EEE;
        position: absolute;
        top: 0;
        right: -2px;
        transform: rotate(10deg);
      }

      img {
        margin-left: 13px;
        transform: rotate(180deg);
        transition: 0.3s ease;
      }
    }
  }

  &__button-wrap,
  &__button-group {
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
    }
  }

  &__button-wrap {
    justify-content: space-between;
    @include sp {
      flex-direction: column;
    }

    &--center {
      justify-content: center;
      margin-top: 40px;
    }

    &--space-between {
      justify-content: space-between;
    }

    &--flex-end {
      justify-content: flex-end;
    }

    &--m20 {
      margin-top: 20px;
    }
  }

  &__button-text {
    font: {
      family: $hiragino;
      size: 12px;
    }
    margin-right: 35px;
    @include sp {
      margin-right: 0;
    }

    &.blue {
      color: #005BAB;
    }
  }

  &__button-group {
    @include sp {
      width: 100%;
      flex-wrap: wrap;
    }

    .form__button-text.blue {
      @include sp {
        margin-bottom: 3px;
      }
    }

    &:first-child {
      @include sp {
        flex-direction: row;
        flex-wrap: nowrap;
        margin-bottom: 10px;
      }
    }

    &--column {
      @include sp {
        flex-direction: column !important;
        margin-bottom: 0 !important;
      }
    }

    .button.square {
      @include sp {
        width: 40px;
      }
    }

    .button {
      margin: 0;
      justify-content: center;
      @include sp {
        width: 100%;
      }

      &:not(:last-child) {
        margin-right: 20px;
        @include sp {
          margin: 0 10px 5px 0;
        }
      }
    }

    &--no-margin-sp {
      .button {
        @include sp {
          margin-right: 0 !important;
        }
      }
    }

    &--centered {
      margin: 0 auto;
      justify-content: center;
      @include sp {
        max-width: 90%;

        .button {
          &:not(:last-child) {
            margin: 0;
            margin-bottom: 10px;
          }
        }
      }
    }

    &--right {
      width: 1080px;
      max-width: 90%;
      margin: 0 auto;
      justify-content: flex-end;
    }

    &--p40 {
      padding-bottom: 40px;
    }

    &--mt20 {
      margin-top: 20px;
    }

    &.mt10-sp {
      @include sp {
        margin-top: 10px;
      }
    }
  }

  &.active {
    .form__pull img {
      transform: rotate(0);
    }
  }

  &__complete-message {
    margin-bottom: 38px;
    font-size: 16px;
    text-align: center;

    &--login {
      margin-bottom: 30px;
      line-height: getem(16, 26);
      @include sp {
        font-size: 14px;
      }
    }

    &.mb15 {
      margin-bottom: 15px;
    }

    &--mb25 {
      margin-bottom: 25px;
    }
  }

  &__subitem {
    position: relative;
    display: flex;
    @include sp {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &.hide {
      display: none;
    }
  }

  &__sublabel {
    font-size: 14px;
    width: 127px;
    max-height: 30px;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    @include sp {
      width: auto;
      max-height: unset;
    }

    &--smaller {
      width: 90px;
      @include sp {
        width: auto;
      }
    }

    &--smallest {
      width: 68px;
    }

    .question {
      margin-left: 7px;
    }

    label {
      font-weight: $bold;
    }

    &.w115 {
      width: 115px;
    }

    &.mb10 {
      margin: 10px 0;
    }
  }

  &__input-subwrap {
    width: 100%;
    @include sp {
      margin-top: 10px;
    }

    &.flex {
      display: flex;
      align-items: center;
      @include sp {
        flex-direction: column;
        align-items: flex-start;
      }
    }

    &.flex-sp {
      display: flex;
      align-items: center;
    }

    .js-scale-selection {
      @include sp {
        margin: 10px 0 0;
      }
    }
  }

  &__textarea {
    width: 100%;
    min-height: 150px;
    border: 1px solid #D5D5D5;
    border-radius: 4px;
    font-family: $hiragino;
    padding: 5px 15px;
    resize: vertical;

    &::placeholder {
      opacity: 0.3;
    }
  }

  &__subblock {
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
      align-items: flex-start;
    }

    &:not(:last-child) {
      margin-right: 56px;
      @media screen and (max-width: 1080px) {
        margin-right: 15px;
      }
      @include sp {
        margin: 0 0 10px;
      }
    }

    button {
      margin-left: 17px;
      flex-shrink: 0;
    }
  }

  &__note {
    font-size: 14px;
    margin-left: 8px;
    opacity: 0.3;

    &--newline {
      margin: 7px 0 0;
    }

    &--red {
      display: block;
      font-size: 12px;
      margin: 5px 0 0;
      opacity: 1;
      color: #E60000;
    }
  }

  &__inline-text {
    font-size: 14px;

    &:first-child {
      margin-right: 14px;
      @include sp {
        margin: 0 0 5px;
      }
    }

    &:last-child {
      margin-left: 10px;
      @include sp {
        margin: 5px 0 0;
      }
    }
  }

  &__select-duplicate {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    button {
      margin-left: 17px;
      @include mx1200 {
        margin-left: 10px;
      }
      @include sp {
        margin: 0;
        position: absolute;
        top: 0;
        right: 0;
      }
    }

    .form__subitem {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__select-container {
    display: none;

    &.show {
      display: block;
    }

    & > .form__subitem {
      margin: 20px 0;

      &:first-child {
        margin: 0 0 10px;
      }
    }
  }

  &__flex {
    display: flex;
    @include mx1200 {
      flex-direction: column;
    }

    .form__item {
      border-bottom: 1px solid #D5D5D5;

      &:not(:last-child) {
        border-right: 1px solid #D5D5D5;
        @include mx1200 {
          border-right: 0;
          border-bottom: 1px solid #D5D5D5 !important;
        }
      }
    }

    &.border-right {
      border-right: 1px solid #D5D5D5;
    }
  }

  &__subflex {
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column;
      align-items: flex-start;
      margin: 10px 0 0;
    }

    span {
      flex-shrink: 0;
      white-space: nowrap;
      @include sp {
        margin: 10px 0;
      }
    }

    &--wrap {
      flex-wrap: wrap;
    }
  }

  &__input-flex-end {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;

    span {
      margin-bottom: 0;
      @include sp {
        margin-right: 10px;
      }
    }

    button {
      margin-right: 0;
      margin-top: 0 !important;
    }
  }

  &__forgot {
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 13px;
    }

    &--agreement {
      &:not(:last-child) {
        margin-bottom: 17px;
      }
    }

    &--half {
      &:not(:last-child) {
        margin: 0 20px 0 0;
      }
    }
  }

  &__forgot-container {
    width: 400px;
    margin: 0 auto 40px;
    max-width: 100%;

    &--agreement {
      margin-bottom: 20px;
    }
  }

  &__agreement {
    margin-top: 40px;
  }

  &__agreement-head {
    margin-bottom: 36.8px;
  }

  &__agreement-title {
    display: block;
    text-align: center;
    font-size: 14px;
    margin-bottom: 15px;
  }

  &__forgot-flex {
    display: flex;
    justify-content: space-between;
  }

  &__authentication {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 40px;

    &--mb19 {
      margin-bottom: 19px;
    }
  }

  &__authentication-item {
    &:not(:last-child) {
      margin-right: 30px;
      @include sp {
        margin-right: 20px;
      }
    }
  }

  &__resend {
    font-size: 12px;
    text-align: center;
    margin-top: 8px;
    color: #005BAB;
  }

  &__error {
    display: block;
    font-size: 12px;
    color: #E60000;
    margin: 0 auto 18px;
    width: 360px;
    @include sp {
      width: 90%;
    }
  }

  &__block {
    @include sp {
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: 25px;
      @media screen and (max-width: 1197px) {
        margin-bottom: 10px;
      }
      @include sp {
        margin-right: 0;
      }
    }
  }

  &__text {
    margin: 0 12px 0 0 !important;
    @include sp {
      display: block;
      margin: 0 0 5px !important;
    }
  }

  &__counter {
    font-size: 14px;
    font-weight: $bold;

    .big {
      font-size: 16px;
    }

    &--absolute {
      position: absolute;
      right: 56px;
      top: 45px;
      @include sp {
        right: 5%;
        top: 20px;
      }
    }
  }

  &__input-subrow {
    width: 100%;
  }

  &__search-by {
    display: flex;
    align-items: center;
    width: calc(100% - 147px);
    @include mx1200 {
      width: 100%;
      margin-top: 10px;
    }
    @include sp {
      flex-direction: column;
    }
  }
}
