.input {
  &__list {
    width: 600px;
    display: flex;
    flex-direction: column;
  }

  &__item,
  &__row {
    display: flex;
  }

  &__item {
    margin-bottom: 10px;
  }

  &__label {
    width: 155px;
  }

  &__wrap {
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-right: 50px;
    }
  }

  &__date-wrapper {
    width: 200px;
    margin-right: 10px;
    @include sp {
      width: 100%;
      margin: 0 0 10px 0;
    }
  }

  &__date {
    width: 200px;
    height: 30px;
    border: 1px solid #D5D5D5;
    border-radius: 4px;
    font: {
      family: $hiragino;
      size: 16px;
    }
    padding-left: 15px;
    background: {
      image: url("/img/icons/icon-date.svg");
      size: 18px 16px;
      repeat: no-repeat;
      position: center right 10px;
    }
    @include sp {
      width: 100%;
      font-size: 14px;
    }

    &::placeholder {
      opacity: 0.3;
      color: #333;
    }

    &--big {
      height: 40px;
      color: #333;
    }

    &--md {
      width: 360px;
      height: 40px;
      @include sp {
        width: 100%;
      }
    }

    &--blue {
      background-color: #F5FAFF;
      color: $primary;
      border: 1px solid $primary;
      background: {
        image: url("/img/icons/icon-date-blue.svg");
        size: 18px 16px;
        repeat: no-repeat;
        position: center right 10px;
      }

      &::placeholder {
        color: $primary;
      }
    }
  }

  &__text {
    width: 300px;
    height: 30px;
    border: 1px solid #D5D5D5;
    border-radius: 4px;
    font-size: 14px;
    padding-left: 15px;
    font-weight: $medium;
    max-width: 100%;
    color: $gray;
    @include sp {
      width: 100%;
    }

    &.mr10 {
      margin-right: 10px;
      @include sp {
        margin: 0 0 10px;
      }
    }

    &.large {
      width: 433px;
    }

    &.big {
      width: 480px;
      height: 40px;
      @include sp {
        width: 100%;
      }
    }

    &.length {
      width: 84%;
      @include sp {
        width: 100%;
      }
    }

    &.full {
      width: 89%;
      @include sp {
        width: 100%;
        margin-bottom: 5px;
      }
    }

    &.xs {
      width: 80px;
    }

    &.sm {
      width: auto;
      border: 0;
    }

    &.md {
      width: 150px;
      @include sp {
        margin-bottom: 5px;
      }
    }

    &.small {
      width: 342px;
      @media screen and (max-width: 1200px) {
        width: 100%;
      }
      @include sp {
        width: 100%;
      }
    }

    &.w200 {
      width: 200px;
      @include sp {
        margin-bottom: 5px;
      }
    }

    &.w400 {
      width: 400px;
    }

    &.w355 {
      width: 355px;
    }

    &.w300 {
      width: 300px;

      & ~ span {
        @include sp {
          margin: 5px 0 0;
        }
      }
    }

    &.w250 {
      width: 250px;
    }

    &.w341 {
      width: 341px;
    }

    &.smallest {
      width: 106px;
      @include sp {
        max-width: 100%;
      }
    }

    &.center {
      text-align: center;
      padding: 0;
    }

    &.full {
      width: 100%;
    }

    &::placeholder {
      opacity: 0.3;
      color: #333;
    }

    &--modal {
      height: 40px;
      margin-top: 13px;
    }

    &--email-full {
      width: 100%;
      height: 40px;
    }

    &--questionnaire {
      width: 595.32px;
      @include sp {
        width: 100%;
      }
    }

    &--scale {
      width: 529.32px;
      @include sp {
        width: 100%;
      }
    }

    &--big {
      height: 40px;
    }

    &--no-br {
      border-radius: 0;
    }

    &--auth {
      width: 45px;
      height: 50px;
      margin: 0 5px;
      padding: 0;
      text-align: center;
      @include sp {
        margin: 0 3px;
        width: 35px;
        height: 40px;
      }
    }
  }

  &__delivery-dest {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    @include sp {
      flex-direction: column;
    }

    &--small {
      justify-content: space-between;
      width: calc(100% - 115px);
      @include sp {
        width: 100%;
        margin-top: 10px;
      }

      .multiselect [type=checkbox]:checked + span,
      .multiselect [type=checkbox]:not(:checked) + span {
        font-size: 14px;
        line-height: getem(14, 26);
        margin: 0;
        padding: 0 20px;
      }
    }
  }

  &__delivery-dest-multiselect {
    width: 42%;
    height: 140px;
    border: 1px solid $alto;
    border-radius: 5px;
    padding: 10px 0;
    background: $athens;
    font-family: $hiragino;
    background-color: transparent;
    color: #333 !important;
    overflow: auto;
    @include sp {
      width: 100%;
    }

    select {
      width: 100%;
      height: 100%;
      font-size: 16px;

      option {
        padding: 0 17px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;

        &:checked {
          background: #005BAB linear-gradient(0deg, #005BAB 0%, #005BAB 100%);
        }
      }

      &::-webkit-scrollbar {
        display: none;
      }
    }

    &.multiselect-destination {
      select {
        background: $white;
      }
    }
  }

  &__delivery-dest-togglers {
    margin: 0 2.63%;
    width: 80px;
    @include sp {
      margin: 3% 0;
    }

    &--small {
      margin: 0 1.47%;
      @include sp {
        margin-bottom: 10px;
      }
    }
  }

  &__note {
    font-size: 12px;
    display: block;
    margin: 5px 0 0 !important;
    opacity: 0.3;
    width: 100% !important;
    @include sp {
      margin: 0 !important;
    }
  }
}
