.list {
  &__button-title {
    text-align: right;
    @include sp {
      text-align: left;
    }

    .button {
      margin-left: 0;
      @include sp {
        margin-top: 10px;
      }
    }
  }

  &__download {
    display: flex;
    align-items: center;
    margin-top: 5.5px;
    font-weight: $bold;
    font-size: 14px;
    color: #005BAB;
  }

  &__download-icon {
    margin-left: 10.2px;
    width: 17.16px;
  }

  &__checkbox {
    display: flex;
    align-items: center;
    margin-right: 40px;
    @include sp {
      margin: 10px 0;
    }

    .question {
      margin-left: 7px;
    }
  }

  &__files-scroll {
    position: relative;
    z-index: 2;
    overflow: auto;
    width: 100%;
    height: 597px;

    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
    }

    &::-webkit-scrollbar-track {
      background: #E0E0E0;
      border-radius: 100px;
    }

    &::-webkit-scrollbar-thumb {
      background: #004B9E;
      border-radius: 100px;
    }
  }

  &__folder-header {
    position: relative;
    padding: 19px 32.8px 19px 171px;
    display: flex;
    align-items: center;

    &--start {
      padding-left: 22.3px;
    }

    .list__button {
      margin-right: 21.3px;
    }
  }

  &__arrow {
    margin-right: 10px;
  }

  &__folder-label {
    display: flex;
    align-items: center;
    cursor: pointer;
    flex-shrink: 0;

    &.active {
      .list__arrow {
        transform: rotate(90deg);
      }
    }
  }

  &__folder-icon {
    margin-right: 10px;
  }

  &__folder-name {
    font-weight: $bold;
    font-size: 12px;
    color: #005BAB;
  }

  &__content {
    padding-left: 194px;

    &--start {
      padding-left: 23px;
    }
  }

  &__subcontent {
    padding-left: 70px;
    display: none;

    &.active {
      display: block;
    }

    &--start {
      padding-left: 109px;
    }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 10px 0;
    min-height: 62px;

    &.ui-sortable-helper {
      font-family: $hiragino;
      transform: scale(1.1);
      padding-left: 15px !important;
      border-top: 0 !important;
      background-color: $white;
      box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);

      &::before,
      &::after {
        display: none;
      }

      .list__item-name {
        width: 154px !important;
      }
    }

    &--start {
      padding-left: 23px;

      .checkbox {
        margin-right: 10px;

        span {
          padding-left: 20px;
        }
      }
    }
  }

  &__item-icon {
    margin-right: 25.7px;

    &--start {
      margin-right: 17px;
      @include sp {
        width: 25px;
        margin-right: 9px;
        flex-shrink: 0;
      }
    }
  }

  &__item-name {
    font-size: 12px;
    font-weight: $bold;
    width: 188px;
    flex-shrink: 0;
    padding-right: 10px;
    color: #005BAB;

    &--start {
      width: 256px;
    }
  }

  &__item-people {
    cursor: pointer;
    width: 72px;
    padding-left: 11px;
    flex-shrink: 0;
    font-size: 12px;
    font-weight: $bold;
    color: #005BAB;
  }

  &__description {
    font-size: 12px;
    line-height: getem(12, 20);
    padding: 0 24px;

    &--start {
      padding: 0 10px;
      width: 380px;
      flex-shrink: 0;
    }
  }

  &__radio {
    position: absolute;
    left: -151px;
  }

  &__comparison {
    position: absolute;
    left: -80px;
  }

  &__folder {
    background-color: #F5FAFF;

    .list__item {
      min-height: 57px;
      padding: 5px 0;
      border-top: 1px solid #D5D5D5;

      &::before {
        content: '';
        position: absolute;
        top: -1px;
        left: -70px;
        width: 18.81px;
        height: 50%;
        border-left: 1px solid #D5D5D5;
        border-bottom: 1px solid #D5D5D5;
      }

      &:not(:last-child) {
        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: -70px;
          width: 1px;
          height: 50%;
          background-color: #D5D5D5;
        }
      }
    }

    .list__item-icon {
      position: absolute;
      left: -28px;
      margin-right: 0;
    }

    .list__item-name {
      width: 161px;
    }

    .list__radio {
      left: -221px;
    }

    .list__comparison {
      left: -150px;
    }

    &--start {
      .list__item {
        &::before {
          left: -45px;
        }

        &::after {
          left: -45px !important;
        }
      }

      .list__item-icon {
        position: static;
        margin-right: 10px;
        @include sp {
          margin-right: 9px;
        }
      }

      .list__item-name {
        width: 154px;
      }
    }
  }

  &__block {
    list-style-type: none;

    &:not(:first-child) {
      border-top: 1px solid #D5D5D5;
    }

    &.ui-sortable-helper {
      transform: scale(1.1);
      border: 0 !important;
      background-color: $white;
      box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);

      &::before,
      &::after {
        display: none;
      }
    }

    &.ui-folder-container {
      opacity: 0.5;
      transition: 0.3s ease;
    }
  }

  &__modal {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin: 30px 0 40px;
    @include sp {
      flex-direction: column;
    }
  }

  &__modal-image {
    margin-right: 40px;
    flex-shrink: 0;
    @include sp {
      display: block;
      margin: 0 auto 30px;
    }
  }

  &__modal-block {
    &:not(:last-child) {
      margin-bottom: 34.5px;
    }
  }

  &__modal-title {
    font-size: 20px;
    letter-spacing: 0.02em;
    padding-bottom: 5px;
    border-bottom: 1px solid #D5D5D5;
    margin-bottom: 10.5px;
  }

  &__modal-text {
    font-size: 14px;
    line-height: getem(14, 24);

    ul {
      font-size: 16px;
      line-height: getem(16, 24);
      font-weight: $bold;
      margin-bottom: 10px;
    }

    li {
      &::before {
        content: '・';
        display: inline-block;
        font-feature-settings: normal;
      }
    }
  }

  &__modal-note {
    font-size: 12px;
    color: #999;
    line-height: getem(12, 20);
  }

  &__button {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-right: 54px;

    .js-table-row {
      position: relative;
    }

    .button__content-wrap {
      z-index: 1;
    }
  }

  &__files {
    width: 984px;

    & > li {
      &:not(:first-child) {
        border-top: 1px solid #D5D5D5;
      }

      &:first-child {
        .list__folder-header .list__button,
        &.list__item {
          .button__content-links {
            &::before {
              top: -2px;
              border-width: 0 4px 5px 4px;
              border-color: transparent transparent #005bab transparent;
              @include sp {
                top: auto;
                border-width: 9px 7.5px 0 7.5px;
                border-color: #005bab transparent transparent transparent;
              }
            }
    
            &::after {
              top: -3px;
              border-width: 0 4.5px 6px 4.5px;
              border-color: transparent transparent $white transparent;
              @include sp {
                top: auto;
                border-width: 6.5px 5.5px 0 5.5px;
                border-color: $white transparent transparent transparent;
              }
            }
          }
    
          .button__content-wrap {
            top: 29px;
            padding: 6px 0 0;
            bottom: auto;
            @include sp {
              top: auto;
            }
          }
        }
      }
    }
  }

  &__button-hover {
    padding: 0 !important;
  }
}
